import React from 'react';
import FancyBorder from './FancyBorder';
import Music from './Music';
import Accompaniment from './Accompaniment';
import Movie from './Movie';
import Publishing from './Publishing';
import {selectedRowInfo} from './Menu';
import * as CassetteButtons from './CassetteButtons';
import * as Menu_ from './Menu';
import Modal from './Modal';
import AudioTracks from './AudioTracks';
import * as Movie_ from './Movie';

class MediasetEditor extends React.Component {
  //function MediasetEditor(props) {
  constructor(props) {
    super(props); //??? nec?
    this.forceMediasetEditorUpdate = this
    .forceMediasetEditorUpdate
    .bind(this);

  }

  forceMediasetEditorUpdate(e) {
    this.forceUpdate();
  }

  render() {
    let fred = selectedRowInfo;
    let martha = Menu_.yMenuMounted;
    if (fred) {
    return (
      <React.Fragment>
        <div className="mediasetEditorGridMargin twelveColGrid">
          <Music/>
          <Accompaniment forceMediasetEditorUpdate={this.forceMediasetEditorUpdate}/>
          <Movie/>
 
          <Publishing/>
          <div className="wholeGridRow whiteBg">
            {CassetteButtons.makeComponent(this.props.buttons, selectedRowInfo.original)}
          </div>
        </div>
      </React.Fragment>
    );
  } else {
    return null;
  }
  }
}
//<FancyBorder color="blue"> </FancyBorder>

export default MediasetEditor;
