import React from 'react';
//import ReactDOM from 'react-dom';
import ReactTable from 'react-table';
import "react-table/react-table.css";
import {ReactTableDefaults} from 'react-table'

import * as CassetteButtons from './CassetteButtons';
import * as GCS from '../GCS';
import * as AppState from '../AppState';
import Modal from './Modal';
import MenuInfo from './MenuInfo';
import * as Utils from '../Utils';

export const publishingStateDescriptions = [
  "Editing",
  "Files generated",
  "User testing",
  "Ready to publish",
  "Awaiting mindofsound approval",
  "Published"
];
export const EDITING = 0;
export const FILES_GENERATED = EDITING + 1;
export const USER_TESTING = FILES_GENERATED + 1;
export const READY_TO_PUBLISH = USER_TESTING + 1;
export const AWAITING_MINDOFSOUND_APPROVAL = READY_TO_PUBLISH + 1;
export const PUBLISHED = AWAITING_MINDOFSOUND_APPROVAL + 1;

export const NO_SELECTION = -1;
export const SELECT_ROW_WITH_HIGHEST_INDEX = -2; //Magic no!
export const NO_SELECTION_CHANGE = -3;

export var iSelectedRow = SELECT_ROW_WITH_HIGHEST_INDEX; // With sorting set to desc = true this will correspond on menu creation to first row.
export function setSelectedRow(i) {
  iSelectedRow = i;
  //if (iSelectedRow > - 0) {
  //  selectedRowInfo = {};
  //  selectedRowInfo.original = menuData[i];
  //}
 // let fred = 0;
}

export function setSelectionRowFromSelectionParam(iSelectionParam, iRowsParam) {
  let iSelection = iSelectionParam || NO_SELECTION_CHANGE;
  let iActualSelectedRow
  switch (iSelection) {
    case NO_SELECTION_CHANGE:
      iActualSelectedRow = 222;
      break;
    case SELECT_ROW_WITH_HIGHEST_INDEX:
      setSelectedRow(iRowsParam - 1);
      break;
    case NO_SELECTION:
      setSelectedRow(NO_SELECTION); // -1
      break;
    default:
      setSelectedRow(iSelection);
  }
}

export let menuData;
export function setMenuData(d) {
  menuData = d
}
export let yMenuMounted = false;

export let selectedRowInfo; // a bit yucky?

Object.assign(ReactTableDefaults, {
  showPagination: false, // works
  sortable: true, // works
  multiSort: true, // works
  filterable: true, // works - over all columns
  defaultFilterMethod: (filter, row, column) => {
    const id = filter.pivotId || filter.id
    return row[id] !== undefined
      ? String(row[id]).includes(filter.value)
      : true
  },
  defaultSortDesc: false,
  /*defaultSortMethod: (a, b, desc) => {
    // force null and undefined to the bottom
    a = (a === null || a === undefined)
      ? -Infinity
      : a
    b = (b === null || b === undefined)
      ? -Infinity
      : b
    // force any string values to lowercase
    a = a === 'string'
      ? a.toLowerCase()
      : a
    b = b === 'string'
      ? b.toLowerCase()
      : b
    // Return either 1 or -1 to indicate a sort priority
    if (a > b) {
      return 1
    }
    if (a < b) {
      return -1
    }
    // returning 0, undefined or any falsey value will use subsequent sorts or the
    // index as a tiebreaker
    return 0
  },*/
  defaultPageSize: 20,

  className: "-striped -highlight"
  //minRows: 14, // works etc...
})


function getColumns(data) {
  const columns = [];
  const sample = data[0];
  Object
    .keys(sample)
    .forEach((key) => {
      if (key !== '_id') {
        columns.push({accessor: key, Header: key})
      }
    })
  return columns;
}



function computeHeightForScrollOverflow() {
  // Basically we want window client height minus space for my cassette buttons.
  // We could try and make result so that precise no of rows fits in window.... would need to compute row height?
  
  // see demo at - https://react-table.js.org/#/story/fixed-header-w-vertical-scroll
  return (window.innerHeight - 100).toString() + "px"
}


export class Menu extends React.Component {
  constructor(props) {
    super(props); //??? nec?
    this.state = {
      isOpen: true
    };
    this.onRowClick = this
      .onRowClick
      .bind(this);
    this.toggleModal = this
      .toggleModal
      .bind(this);

  }

  componentDidMount(e) {
    yMenuMounted = true;
    //this.forceUpdate();
  }

  onRowClick = (state, rowInfo, column, instance) => {
    // This is called for each row of table when building table! So we can use this to programmatically select a row!!
    if (!rowInfo) {
      return {}
    }
    if (iSelectedRow == rowInfo.index) {
      selectedRowInfo = rowInfo;
    }
    return {
      style: {
        //background: (rowInfo.row.age > 26) ? 'green' : 'red'
        background: (rowInfo.index == iSelectedRow)
          ? "#aaffaa"
          : null
      },
      onClick: () => {
        // this.setState({rowsIdsArray:
        // this.state.rowsIdsArray.concat([rowInfo.row.id])});
        iSelectedRow = rowInfo.index;
        selectedRowInfo = rowInfo; //.original;
        //instance.forceUpdate();
        this.forceUpdate(); //this.forceUpdateHandler(); // this causes rerender including cassette buttons which will get their new selectedow data! Inefficient!?
      }
    }
  }
  /*
  forceUpdateHandler() {
    this.forceUpdate();
  };
*/
  toggleModal = () => {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }


  /* buildPreview(row) {
    // this.props.data.bucketName this.props.data.accompFileName + switch on
    // location here!!??
    row.original.bucketName + row.original.accompFileName

    return "https://storage.googleapis.com/www.mindofsound.com/0/img/jsoneditor-icons.png";
  }*/

  render() {

    return (
      <div>
         <div>
        <ReactTable
          data={this.props.data}
          columns={this.props.columns}
          defaultSorted={this.props.defaultSorted}
          getTrProps={this.onRowClick}
          className="-striped -highlight"
          defaultPageSize={20}
          style={{
            height: computeHeightForScrollOverflow()// This will force the table body to overflow and scroll, since there is not enough room
          }}
          SubComponent={row => {
          return (<MenuInfo row={row} yVisible={true}/>);
        }}/>
        </div>
        <div>
          {CassetteButtons.makeComponent(this.props.buttons, this.props.data[iSelectedRow])}
        </div>
      </div>
    )
  }
}
