export function pushNewAudioTrack() {
  // Only called when creating new track from button press We keep the player
  // track as last elt of array (so it displays last)
  // audioTrackArray[audioTrackArray.length] =
  // audioTrackArray[audioTrackArray.length - 1]; // copy player track up one
  let newFileId = getMaxNonPlayerFileIdInAudioTrackArray() + 1;
  let newInd = getFirstFreeEltIndBeforePlayerInAudioTrackArray();
  if (newFileId != newInd) {
    alert("newFileId != newInd");
  }
  audioTrackArray[newInd] = {
    fileId: newFileId, // simply ensure uniqueness (we don't use maxFileId for anything else but identity and order of creation)
    audioObject: null,
    yMix: false, //true, // initial value
    volume: 1.0,
    desc: "New movie track",
    yMovieTrack: false,
    deletable: true
  };
  if (existingWavFiles[newFileId]) {
    //AudioTrack_.saveAudioTracks();
    audioTrackArray[newFileId].fileUnexpectedlyExists = true;
  }
}

function createAudioTrack(fileId) {
  // Called when recreating audioTrackArray from files and
  // accompaniment.audio.tracks
  if (audioTrackArray[fileId] != null) {
    alert("warning! audioTrackArray elt already exists");
    fileId = audioTrackArray[audioTrackArray.length - 1].fileId + 1; // simply ensure uniqueness (we don't use maxFileId for anythin else but identity and order of creation)
  } else {
    audioTrackArray[fileId] = {
      fileId: fileId,
      audioObject: null,
      yMix: true, // initial value
      volume: 1.0,
      desc: "New movie track",
      yMovieTrack: false,
      deletable: true
    }

  }
}

export function getAudioTrackByFileId(fileId) {
  let audioTrack;
  let yFound = false;
  for (var i in audioTrackArray) {
    audioTrack = audioTrackArray[i];
    if (audioTrack != null && audioTrack.fileId == fileId) {
      yFound = true;
    }
  }
  return yFound
    ? audioTrack
    : null;
}

export function getMaxNonPlayerFileIdInAudioTrackArray() {
  let audioTrack;
  let maxFileId = -1;
  for (var i in audioTrackArray) {
    audioTrack = audioTrackArray[i];
    if (audioTrack != null) {
      if (audioTrack.fileId != PLAYER_TRACK_ARRAY_IND && audioTrack.fileId > maxFileId) {
        maxFileId = audioTrack.fileId;
      }
    }
  }
  return maxFileId;
}

export function getFirstFreeEltIndBeforePlayerInAudioTrackArray() {
  /*let savePlayerTrack = audioTrackArray[PLAYER_TRACK_ARRAY_IND]
  audioTrackArray[PLAYER_TRACK_ARRAY_IND] = undefined;
  let freeInd = audioTrackArray.length; // length still at 1001!!??
  audioTrackArray[PLAYER_TRACK_ARRAY_IND] = savePlayerTrack;
  return freeInd;*/

  let arrayLen = audioTrackArray.length;
  for (let i = arrayLen - 2; i >= 0; i--) { // start searching one below player elt
    if (audioTrackArray[i]) {
      return i + 1;
    }
  }
  alert("Failed to find free elt");
  return 500; // fudge!
}

function audioTrackArrayToSavedTrackData() {
  // Our javascript array is sparse but lua array is not (that's why array elt
  // contains fileId)
  let destInd = 0;
  AppState.accompaniment.audio.tracks = [];
  let fred = AppState.accompaniment.audio.tracks;
  let martha = audioTrackArray;
  for (var index in audioTrackArray) { // audioTrackArray may be sparse
    let audioTrack = audioTrackArray[index];
    if (audioTrack != null && Boolean(audioTrack.audioObject)) { // only save recorded tracks!
      let track = {};
      track.fileId = audioTrack.fileId;
      track.yMix = audioTrack.yMix;
      track.volume = audioTrack.volume;
      AppState.accompaniment.audio.tracks[destInd] = track;
      destInd++;
    }
  }
  fred = AppState.accompaniment.audio.tracks;
}
