export function iComputeNonPlayerAudioTracksToMix(fileId) {
  let iTracksToMix = 0;
  for (var key in audioTrackArray) {
    if (audioTrackArray[key] != null && key != 1 && audioTrackArray[key].yMix) { // ie exclude play track
      iTracksToMix++;
    }
  }
  return iTracksToMix;
}
