export function nullOrRemoveNewMovieTrack(fileId) {
  let audioTrack = audioTrackArray[fileId]
  if (audioTrack != null) {
    // if (fileId == audioTrackArray.length - 1) {  audioTrackArray.splice(fileId,
    // 1); // trailing nulls in sparse array also contribute to
    // audioTrackArray.length! } else {
    audioTrackArray[fileId] = undefined; //null;
    //}
    existingWavFiles[fileId] = null;
    // remove any trailing null elts!
    while (!Boolean(audioTrackArray[audioTrackArray.length - 1])) { // While the last element is null or undefined
      audioTrackArray.pop(); // Remove that last element
    }

  } else {
    alert("Track does not exist - so could not remove it!");
  }
}

