export function handleSamples(samplesAsBase64, frameRateParam, normalisationFactor) {
  // reduces size (to save memory) for display (for setting time markers eg
  // firstNoteSecondsIn )
  let allAudioSamples = Utils.convertBase64ToInt8Array(samplesAsBase64); //json.audio.samplesAsBase64);
  frameRate = frameRateParam;
  let aggregate = 0;
  let iCtr = 0;
  let iSampleInd = 0;
  audioSamples = [];
  for (var i = 0; i < allAudioSamples.length; i++) {
    aggregate += allAudioSamples[i] * normalisationFactor;
    iCtr++;
    if (iCtr == iAggregateMembers) {
      audioSamples[iSampleInd] = aggregate / iAggregateMembers;
      aggregate = 0;
      iCtr = 0;
      iSampleInd++;
    }
  }
  // audioTrackArray[0].audioSamples = audioSamples; AudioTrack.audioSamples =
  // audioSamples;
}

function getFirstNoteSecondsIn() {
  return (firstNoteSecondsIn || 0).toString();
}

