  mixAndPlayAllWithFfmpegButtonHandler(e) {
    if (iComputeNonPlayerAudioTracksToMix() > 0) {
      function finishedPlay() {
        if (playFinishedTimeoutId) {
          clearTimeout(playFinishedTimeoutId);
        }
        AppComms
          .sendRequestToApp("stopWavFile", null, function (jsonFromApp) {
            if (!Server.handleError(jsonFromApp)) {
              var fred = 0;
            }
          });
        Popup.popupMessageNew();
      }

      Popup.popupMessageNew("Playing all enabled audio tracks together...", "Stop", finishedPlay);

      Publishing_.mixWavFiles(null, function (jsonFromApp) {
        if (!Server.handleError(jsonFromApp)) {
          let data = {};
          data.fileName = "mixed.wav";
          data.firstNoteSecondsIn = AppState.accompaniment.audio.firstNoteSecondsIn;
          data.volume = 1.0;
          AppComms.sendRequestToApp("stopWavFile", null, function (jsonFromApp) {
            if (!Server.handleError(jsonFromApp)) {
              AppComms
                .sendRequestToApp("playWavFile", data, function (jsonFromApp) {
                  if (!Server.handleError(jsonFromApp)) {
                    playFinishedTimeoutId = setTimeout(finishedPlay, (AppState.movieSecondsDuration - AppState.accompaniment.audio.firstNoteSecondsIn) * 1000);
                  }
                });
            }
          });
        }
      });
    } else {
      alert("No tracks enabled!"); // for mixing!")
    }
  }

