  render() {
    if (Movie_.accompVideo == null) {
      return null;
    }
    //let row = {}; row.original = selectedRowInfo;

    const audioTrackItems = audioTrackArray.map((track) => <AudioTrack
      track={track}
      setMovieSliderPosition={this.props.setMovieSliderPosition}
      forceAudioTracksComponentUpdate={this.forceAudioTracksComponentUpdate}/>);
    let fredzz = selectedRowInfo;
    return (
      <React.Fragment>
        <div className="wholeGridRow whiteBg">
          <br/>
          <b>Audio</b>
        </div>
        {audioTrackItems}
        <div className="wholeGridRow whiteBg">
          <button onClick={this.mixAndPlayAllWithFfmpegButtonHandler}>Play all</button>
        </div>
        <div className="wholeGridRow whiteBg">
          <br/>
          <b>Menu info</b>&nbsp;&nbsp;
          <span className='fa-stack fa-1x' onClick={this.editPreviewHtmlButtonHandler}>
            <i className='fa fa-circle fa-stack-2x buttonNormalStyle'></i>
            {this.state.yMenuInfoVisible
              ? <i className='fa fa-minus fa-stack-1x'></i>
              : <i className='fa fa-info fa-stack-1x'></i>
            }
          </span>
        </div>

        <div className="mycell wholeGridRow">
          <MenuInfo
            row={selectedRowInfo}
            yVisible={this.state.yMenuInfoVisible}
            yEditing={true}/>
        </div>
        <div
          className="mycell wholeGridRow "
          style={Utils.getVisibilityObject(this.state.yMenuInfoVisible)}>
          <textarea
            id="menuInfoTextArea"
            rows="5"
            className="editorGridWith resizeVertical"
            value={this.state.menuInfoTextAreaValue}
            onChange={this.handleMenuInfoTextAreaChange}
            spellcheck="false"/>
        </div>
      </React.Fragment>
    );
  }
