function CassetteButtonz(props) {
  return (
    <span className='fa-stack fa-3x' id={props.iconName} onClick={props.handler}>
      <i className='fa fa-circle fa-stack-2x buttonNormalStyle'></i>
      <i className={'fa ' + props.iconName + ' fa-stack-1x'}></i>
      {/*isClicked ? 'clickedzzz' : 'unclickedzzz'  */}
    </span>
  );
}

class CassetteButton extends React.Component {
  //constructor(props) {
  //  super(props); //??? nec?
  //} 

  //clickHandler = () => {
  //  this.props.this.props.handler()
  //}
  handleClick = () => {
    this.props.handler(this.props.selectedRow);
  }

  render() {
    return (
      <span className='fa-stack fa-3x' id={this.props.iconName} onClick={this.handleClick}>
        <i className='fa fa-circle fa-stack-2x buttonNormalStyle'></i>
        <i className={'fa ' + this.props.iconName + ' fa-stack-1x'}></i>
        {/*isClicked ? 'clickedzzz' : 'unclickedzzz'  */}
      </span>
    );
  }
}


export default CassetteButton;



