export function handleCreate(cb) {
  // No longer requires server call - now precedes folder no with computer name.
  function abort(resp) {
    alert(resp.error);
    Popup.popupMessageNew();
  }
  Popup.popupMessageNew("Creating new media...");
  let data = {};
  AppComms.sendRequestToApp("createMediaset", data, function (jsonFromApp) {
    if (!Server.handleError(jsonFromApp, abort)) {
      let newFolderName = jsonFromApp.data.newFolderName;
      AppState.displayMenu(Menu_.SELECT_ROW_WITH_HIGHEST_INDEX, function (jsonFromApp) { //AppComms.sendRequestToApp("getCreatedMediasetMenuData", null, function (jsonFromApp) {
        if (!Server.handleError(jsonFromApp)) {
          Popup.popupMessageNew();
        }
      });
    }
  });

  //createBucketIfNecFromLoggedInUser(); // also failed on send
}

function modalConfirm(sTitle, msgParam, yesMethod) {
  //https://jqueryui.com/dialog/#modal-confirmation
  var msg = msgParam || "Are you sure?";
  window
    .$("#dialog-confirmz")
    .dialog({title: sTitle});
  document
    .getElementById("dialogConfirmTextSpanId")
    .innerHTML = "<i>" + msg + "</i>";
  AppState.setDialogueConfirmYesCallback(yesMethod); //declared in html!?
  window
    .$("#dialog-confirmz")
    .dialog("open");

}

export function handleDelete(cb) {
  //let fred = Menu_.selectedRowInfo
  if (Menu_.selectedRowInfo != null) {
    var sTitle = Menu_.selectedRowInfo.original.title; // wrong this is folder
    modalConfirm('Delete mediaset: \"' + sTitle + "\"", null, function () {
      Popup.popupMessageNew("Delete mediaset...");
      var data = {}
      data.folder = Menu_.selectedRowInfo.original.folderName; //[firstInvisibleIndex + 1];
      AppComms.sendRequestToApp("deleteMediaset", data, function (jsonFromBrowser) {
        if (!Server.handleError(jsonFromBrowser)) {
          AppState
            .displayMenu(Menu_.SELECT_ROW_WITH_HIGHEST_INDEX, function () {
              Popup.popupMessageNew();
            });
        }
      });
    });
  } else {
    alert("Select a mediaset first!");
  }
}

