import React from 'react';
import ReactDOM from 'react-dom';
import * as AppState from '../AppState';
import * as Utils from '../Utils';
import * as Popup from '../modalPopup';
import * as AppComms from '../AppComms';
import * as Server from '../Server';
import * as Publishing_ from './Publishing';
import * as Menu_ from './Menu';

const alphaRangeRangeStart = 0.01; // prevent antialiasing
const alphaRangeRange = 0.42;
const alphaRangeSliderMultiplier = 100 / alphaRangeRange;
const alphaDropoffRangeStart = 0.0
const alphaDropoffRange = 2;
const alphaDropoffSliderMultiplier = 100 / alphaDropoffRange;
let currentColor;
const defaultColor = "FFFFFF";
function fillAccompDropdown() {
  Utils.clearDropdownOptions("accompDropdownId");
  var dropdown = document.getElementById('accompDropdownId');
  for (var i = 0; i < AppState.mediasetEditables.accompaniments.length; i++) {
    // select.options[i].name == mediasetEditables.accompaniments[accompInd -
    // 1].name; select.options[i].value == i + 1;
    dropdown
      .options
      .add(new Option(AppState.mediasetEditables.accompaniments[i].name, i + 1))
  }
}

function fillPaperDropdown(papers) {
  Utils.clearDropdownOptions("paperDropdownId");
  var dropdown = document.getElementById('paperDropdownId');
  dropdown
    .options
    .add(new Option("Color", 1));
  for (var i = 0; i < AppState.papers.length; i++) {
    let name = AppState.papers[i].name;
    dropdown
      .options
      .add(new Option(name, i + 2));
  }
  var paper = AppState.accompaniment.paper || defaultColor;
  for (var i = 0; i < dropdown.options.length; i++) {
    if (dropdown.options[i].text == paper) {
      dropdown.selectedIndex = i;
    }
  }
  let paperColorPicker = document.getElementById("paperColorPickerId");
  let paperColorPickerSpan = document.getElementById("paperColorPickerSpanId");
  Utils.setVisible(paperColorPickerSpan, dropdown.options[dropdown.selectedIndex].text == "Color");
  if (paper.substring(0, 1) == "#") {
    //paper = "Color";

    paperColorPicker.value = AppState.accompaniment.paper;
    currentColor = AppState.accompaniment.paper;
    //  AppState.setCurrentPaperColor(AppState.accompaniment.paper);
    // Utils.isVisible(paperColorPicker,
    // dropdown.options[dropdown.selectedIndex].text == "Color");

  } else {
    //  AppState.accompaniment.paper = AppState.setCurrentPaperColor(paper);
    // //AppState.defaultPaperColor);
    currentColor = defaultColor;
  }

}

function setSlidersVisibility() {
  var yMask = (AppState.movie.mask.type != "none") && AppState.displayMode.useMask;
  Utils.setVisible(document.getElementById("alphaRangeSliderSpanId"), yMask);
  Utils.setVisible(document.getElementById("alphaDropoffSliderSpanId"), yMask);
}

class Accompaniment extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      accompInd: 0,
      title: 'Piecename'
    };
    this.onAccompChangeHandler = this
      .onAccompChangeHandler
      .bind(this);
    this.onAccompanimentNameChangeHandler = this
      .onAccompanimentNameChangeHandler
      .bind(this);
    this.onPaperDropdownChangeHandler = this
      .onPaperDropdownChangeHandler
      .bind(this);
    this.onPaperColorChangeHandler = this
      .onPaperColorChangeHandler
      .bind(this);
    this.onDefaultNotesColorChangeHandler = this
      .onDefaultNotesColorChangeHandler
      .bind(this);
    this.onMaskChangeHandler = this
      .onMaskChangeHandler
      .bind(this);
    this.onAlphaRangeSliderChangeHandler = this
      .onAlphaRangeSliderChangeHandler
      .bind(this);
    this.onAlphaDropoffSliderChangeHandler = this
      .onAlphaDropoffSliderChangeHandler
      .bind(this);
    this.uploadPickedMovieFileToAppThenConvertAndMakeMovieInfoSnapshotAndHtml = this
      .uploadPickedMovieFileToAppThenConvertAndMakeMovieInfoSnapshotAndHtml
      .bind(this);

  }

  uploadPickedMovieFileToAppThenConvertAndMakeMovieInfoSnapshotAndHtml(event, recType, folderName) {
    var fileData = event.target.files[0];
    let thiz = this;
    let chosenFileBaseName = Utils.removeExtAndDot(fileData.name);
    function abort(resp) {
      alert(resp.error);
      Popup.popupMessageNew();
    }
    Popup.popupMessageNew("Uploading file to app " + fileData.name + " for app");
    AppComms.uploadFileToApp(event, recType, folderName, function (resp) {
      if (!Server.handleError(resp, abort)) {
        Popup.popupMessageNew("Converting chosen file...");
        var data = {};
        data.destSubpath = "records/" + recType + "/" + folderName;
        data.origFileName = fileData.name
        data.cropRect = null; // This signifies to lua convert that this is not publishing conversion
        AppComms.sendRequestToApp("convertMovieFile", data, function (resp) {
          if (!Server.handleError(resp, abort)) {
            Publishing_
              .makeMenuInfoSnapshot(chosenFileBaseName + '_E.mp4', chosenFileBaseName, function (resp) { // _E file was produced for editor by convert (above)
                if (!Server.handleError(resp, abort)) {
                  // AppState.mediasetEditables.publishingState = 1; // ie Files produced Create
                  // empty html file
                  AppComms
                    .sendRequestToApp("saveTextToRecordFile", {
                      "fileName": chosenFileBaseName + ".html"
                    }, function (jsonFromApp) {
                      if (!Server.handleError(resp, abort)) {
                        // delete old file?
                        AppState.accompaniment.fileName = fileData.name;
                        AppState.accompaniment.audio.firstNoteSecondsIn = 0; // so gets recalculated
                        //sendMediasetEditablesToNotesGameAppAndRelaunch();
                        AppState.sendMediasetEditablesToNotesGameApp("update", function (resp) { // saves main.json
                          if (!Server.handleError(resp, abort)) {
                            // Ok - we have all the files for new movie! So only now change menu and show mediaset (changes app launch vars?)
                            // But what if failed? We should surely reinstate previous movie file in grid ???
                            //if (publishingState != published) {  publishingState = 0;?? }
                            AppState
                              .displayMenu(Menu_.SELECT_ROW_WITH_HIGHEST_INDEX, function () { // update menu so that selectRowInfo is valid after movie filename change (used by showMediaset - eg MenuInfo)
                                 AppState.showMediaset(Menu_.selectedRowInfo.original.bucketName, Menu_.selectedRowInfo.original.folderName, true, function () {
                                  let fred = 0;
                                });
                              });
                          }
                        });
                      }
                    });
                }
              });
          }
        });
      }
    });
  }
  
  componentDidMount() {
    fillAccompDropdown(); // ?????? operates directly on dom?
    fillPaperDropdown();
    /*function uploadPickedMovieFileToAppAndMakeMenuInfoSnapshot(event, recType, folderName) {
    AppState.uploadPickedMovieFileToApp()
    makeMenuInfoSnapshot(function (jsonFromApp2) {
      if (!Server.handleError(jsonFromApp2, abort)) {
        AppState.mediasetEditables.publishingState = 1; // ie Files produced
        Popup.popupMessageNew();
      }
    }*/
    AppState.createAndInsertPickFileToHandleCombo("MovieFilePicker", "video/*", this.uploadPickedMovieFileToAppThenConvertAndMakeMovieInfoSnapshotAndHtml, "Change movie file"); // uploadPickedFileToCloud);//insertObjectFromGui, "Confirm video file for upload");
    var MovieFilePickerfileNameLabel = document.getElementById("MovieFilePickerfileNameLabel");
    MovieFilePickerfileNameLabel.innerHTML = AppState.accompaniment.fileName;

    let maskDropdown = document.getElementById("maskDropdownId");
    /*for (var i = 0; i < maskDropdown.options.length; i++) {
      if (maskDropdown.options[i].text == AppState.movie.mask.type) {
        maskDropdown.selectedIndex = i;
      }
    }*/
    maskDropdown.value = AppState.movie.mask.type; // This also sets maskDropdown.selectedIndex correctly!

    setSlidersVisibility();
    var alphaRangeSlider = document.getElementById("alphaRangeSlider");
    alphaRangeSlider.value = (AppState.movie.mask.alphaRange - alphaRangeRangeStart) * alphaRangeSliderMultiplier;
    var alphaDropoffSlider = document.getElementById("alphaDropoffSlider");
    alphaDropoffSlider.value = (AppState.movie.mask.alphaDropoff - alphaDropoffRangeStart) * alphaDropoffSliderMultiplier;
    var martha = AppState.movie.mask.alphaDropoff;

  }

  onAccompChangeHandler(event) {
    //mediaset.accompInd = cb.value;
    let iIndex = event.target.value;
    this.setState({accompInd: iIndex});
    AppState.mediasetEditables.accompInd = iIndex + 1; // adjust for lua
    //accompaniment = mediasetEditables.accompaniments[cb.value];
    alert("sendMediasetEditablesToNotesGameAppAndRelaunch() no longer exists!"); // untested!!
  }

  onAccompanimentNameChangeHandler(cb) {
    AppState.accompaniment.name = cb.target.value;
    fillAccompDropdown();
    this.forceUpdate();
    AppState.sendMediasetEditablesToNotesGameApp();
  }

  onFirstNoteExpandHandler(cb) {}

  onPaperColorChangeHandler(cb) {
    let paper = cb.target.value;
    AppState.accompaniment.paper = paper;
    if (paper.substring(0, 1) == "#") {
      currentColor = paper;
    }
    //AppState.setCurrentPaperColor(cb.target.value);
    AppState.sendMediasetEditablesToNotesGameAppAndRedraw(); // could speed this up without redraw
    var dropdown = document.getElementById('paperDropdownId');
  }

  onPaperDropdownChangeHandler(cb) {
    //accompaniment.paper = cb.target.options[cb.target.selectedIndex].value;
    var text = cb.target.options[cb.target.selectedIndex].text;
    var pickerSpan = document.getElementById("paperColorPickerSpanId");
    // Utils.setVisible(pickerSpan, dropdown.options[dropdown.selectedIndex].text ==
    // "Color");
    if (text == "Color") {
      Utils.setVisible(pickerSpan, true);
      /*
            function doClick() {
                document.getElementById("paperColorPickerId").click();
            }
            setTimeout(doClick,500); // otherwise onPaperColorChangeHandler does not get called call (because elt still invisible!??)
            */
      document
        .getElementById("paperColorPickerId")
        .value = currentColor; //"#FFFFFF";//getHexColor("white");//AppState.defaultPaperColor;
      AppState.accompaniment.paper = currentColor; //"#FFFFFF"; //getHexColor("white");//AppState.defaultPaperColor;
    } else {
      Utils.setVisible(pickerSpan, false);
      AppState.accompaniment.paper = text;
    }
    AppState.sendMediasetEditablesToNotesGameAppAndRedraw(); // could speed this up without redraw

    this.forceUpdate();
  }

  onDefaultNotesColorChangeHandler(cb) {
    AppState.accompaniment.color = cb.target.value;
    AppState.sendMediasetEditablesToNotesGameAppAndRedraw();
    this.forceUpdate();
  }

  onMaskChangeHandler(e) {
    AppState.movie.mask.type = e.target.value;
    AppState.sendMediasetEditablesToNotesGameAppAndRedraw();
    this.forceUpdate();
    setSlidersVisibility();
  }

  onAlphaRangeSliderChangeHandler(cb) {
    AppState.movie.mask.alphaRange = cb.target.value / alphaRangeSliderMultiplier + alphaRangeRangeStart;
    AppState.sendMediasetEditablesToNotesGameApp();
    this.forceUpdate();
  }

  onAlphaDropoffSliderChangeHandler(cb) {
    AppState.movie.mask.alphaDropoff = cb.target.value / alphaDropoffSliderMultiplier + alphaDropoffRangeStart;
    AppState.sendMediasetEditablesToNotesGameApp();
    this.forceUpdate();
  }

  render() {
    return (
      <React.Fragment>
        <div className="wholeGridRow whiteBg">
          <br/>
          <b>Accompaniment :</b>
          &nbsp;&nbsp;
          <select
            name="accompDropdown"
            id="accompDropdownId"
            onChange={this.onAccompChangeHandler}>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
        </div>

        <div className="mycell wholeGridRow">Name&nbsp;&nbsp;
          <input
            type="text"
            name="Accompaniment name"
            id="AccompanimentName"
            value={AppState.mediasetEditables.accompaniments[this.state.accompInd].name}
            size="20"
            onChange={this.onAccompanimentNameChangeHandler}/>

          <label>&nbsp;&nbsp;&nbsp;&nbsp;Paper&nbsp;</label>
          <select
            name="paperDropdown"
            id="paperDropdownId"
            onChange={this.onPaperDropdownChangeHandler}>
            <option>Dummy</option>
          </select>

          &nbsp;&nbsp;&nbsp;&nbsp;
          <span id="paperColorPickerSpanId">
            <input
              type="color"
              id="paperColorPickerId"
              onChange={this.onPaperColorChangeHandler}/>
          </span>
          &nbsp;&nbsp;&nbsp;&nbsp;
          <span id="defaultNotesColorPickerSpanId">Music color&nbsp;&nbsp;
            <input
              type="color"
              id="defaultNotesColorPickerId"
              value={AppState.accompaniment.color}
              onChange={this.onDefaultNotesColorChangeHandler}/></span>
        </div>
        <div className="mycell wholeGridRow">

          <span id="MovieFilePicker">Movie file&nbsp;&nbsp;</span>

        </div>
        <div className="mycell wholeGridRow">

          <label>Mask&nbsp;&nbsp;</label>
          <select
            name="maskDropdown"
            id="maskDropdownId"
            onChange={this.onMaskChangeHandler}>
            <option>none</option>
            <option>ellipse</option>
          </select>
          &nbsp;&nbsp;&nbsp;&nbsp;
          <span id="alphaRangeSliderSpanId">Alpha range
            <input
              type="range"
              id="alphaRangeSlider"
              onChange={this.onAlphaRangeSliderChangeHandler}/>
          </span>
          &nbsp;&nbsp;&nbsp;&nbsp;
          <span id="alphaDropoffSliderSpanId">Dropoff power
            <input
              type="range"
              id="alphaDropoffSlider"
              onChange={this.onAlphaDropoffSliderChangeHandler}/>
          </span>

        </div>
      </React.Fragment>
    );
  }
}

export default Accompaniment;



