  moveToSecondsIn(secondsIn) {
    // So that secondsIn corresponds to center of display
    this
      .props
      .setMovieSliderPosition((secondsIn - halfWaveDisplayWidthSeconds) * 100 / AppState.movieSecondsDuration);
  }

