import React from 'react';
import CassetteButton from './CassetteButton'
import './CassetteButtons.css';

export const makeComponent = (props, selectedRow) => {
  // http://2ality.com/2015/10/property-traversal-order-es6.html
  let buttons = [];
  function addFaPrefix(t) {
    return "fa-" + t;
  }
  var iButtons = props.length/2;
   // for (var key in props) {
     for (var i = 0; i < iButtons; i++) {
      //console.log(key, props[key]);
      //var handlerName = props[key];
      // deduce icon name from handler name - NOT WORTH IT!
      //print (key);
     // buttons.push(<CassetteButton iconName={addFaPrefix(props[key].icon)} handler={props[key].handler}/>);
     var iBase = i * 2;
      buttons.push(<CassetteButton iconName={addFaPrefix(props[iBase])} handler={props[iBase + 1]} selectedRow={selectedRow}/>);
    }
    return ( 
      <div className='cassetteButtons cassetteControl' >{buttons}</div>
  );
 
} 

