export function callServer(baseUrl, subPathAndParams, callback, yPost) {
  let method = "GET";
  if (yPost) {
    method = "POST";
  }
  let xhttp = new XMLHttpRequest();
  xhttp.onreadystatechange = function (evt) {
    if (xhttp.readyState == XMLHttpRequest.DONE && xhttp.status == 200) {
      callback(xhttp.responseText);
    }
  };
  xhttp.onerror = function (evt) {
    callback("onerror");
  }
  xhttp.onabort = function (evt) {
    callback("onabort");
  }
  //xhttp.onload = function (evt) { 	var z = 0; }
  xhttp.onprogress = function (evt) {
    console.log("progress");
  }

  let url = baseUrl + subPathAndParams; //gcs/getUserInfo";
  // let url =
  // "http://dev.mos-cloud.appspot.com/gcs/getUserInfo";//"http://localhost:8080/g
  // c s/getUserInfo";
  xhttp.open(method, url, true); // true means async - ie call onreadystatechange when response. Note that at first with GET I got always the same strange responseText - Value and some woird string. Bit when changed to POST it worked and when changing back to GET it still worked!!????
  xhttp.send();

}
