export function callServerMethod(baseUrl, sServerMethod, sParams, callback, yPost) {
  // expects return to be a json string!
  callServer(baseUrl, sServerMethod + '?' + sParams, function (s) {
    var resp;
    try {
      resp = JSON.parse(s);
    } catch (e) {
      resp = resp; // we allow simple string (error) return as we do for app comms - and we will use same handleError function as for app comms!
    }
    callback(resp);
  }, yPost);
}

export function getStringFromServerNB(sServerMethod, sParams, callback) {
  callServer(sServerMethod + '?' + sParams, callback);
}

export function callCloudServerMethod(sServerMethod, sParams, callback, yPost) {
  callServerMethod(AppState.serverUrl, sServerMethod, sParams, callback, yPost);
}

export function handleError(response, errorCallbackParam) {
  var yError = false;
  if (response) {
    var errorCallback = errorCallbackParam || defaultErrorCallback;
    if (typeof response === 'string') {
      response = JSON.parse(response); ///!!jsonFromApp);
    }

    function defaultErrorCallback(response) {
      alert("Error: " + (response.error || response.data.error)); //brackets around or are nec!
    }
    //var yError = (response.error != null && response.error.length > 0);
    if (response.data == null && response.error == null) { // handles getting mediaseteditables (should put them under data!)
      yError = false;
    } else {
      if (response.data) {
        yError = (response.data.error != null && response.data.error.length > 0);
      } else if (response.error) {
        yError = (response.error != null && response.error.length > 0);
      }

    }
    if (yError) {
      errorCallback(response);
    }
  } else {
    let fred = 0; // No response means ok!?
  }
  return yError;
}

export function errorCallback(callback, sError) {
  // Make it look like an app response!
  callback({
    "data": {
      "error": sError
    }
  });
}
