function createBucketIfNecFromLoggedInUser() {
  // No! - we can only allow logged in user to create his own bucket and to do
  // this in javascript we must reload page!! (ie window.location = ...) blocking
  // call
  var url = 'http://' + AppState.webVersion + '.mos-cloud.appspot.com/gcs/createBucketIfNecFromLoggedIn?d=2'; // + Math.random();
  let xhr = new XMLHttpRequest();
  xhr.open('GET', url, false); // async = false
  //xhr.setRequestHeader('Access-Control-Allow-Origin', '*');
  xhr.send(null);
  if (xhr.status === 200) {
    return xhr.responseText == 'Not Found'
      ? null
      : xhr.responseText
    //return xhr.responseText;
  } else {
    //alert("failed to load json");
    return null;
  }

}


