/*
Publishing files are produced with the help of ffmpeg which is accessed by the app.

PUBLISHING STATES:
  0: Unpublished
  1: Files produced
  2: Files uploaded
  3: Published

SUFFIXES
  _E: Produced from original mp4 purely for creator editor (ie not too big for browser etc). No files are produced from this.
  _P: Result of replaceVideoAudio. _L, _M and _H versions of movie are subsequently produced from this by ConvertVideo.lua
  _L: Low res of mp4
  _M: Medium res of mp4 (if original mp4 itself is of high enough res)
  _H: High res of mp4 - not currently implemented.

Published files:
  Let movieFileNameBase = <main.json.accompaniments[0].movie.fileName minus the ext.
  main.json                 : File names, Cropping info etc for mediaset.
  <main.json.notesFileName> : .mid file
  <movieFileNameBase>_L.mp4 : Low res of movie
  <movieFileNameBase>_M.mp4 : High res of movie
  <movieFileNameBase>_H.mp4 : High res of movie
  <movieFileNameBase>.mp3   : Audio clip of movie for MenuInfo
  <movieFileNameBase>.jpg   : Snapshot of movie for MenuInfo
  <movieFileNameBase>.html  : Html which appears next to snapshot in MenuInfo

*/
