export function replaceSuffixChar(sPath, sNewChar) {
  let iSuffixChar = sPath.lastIndexOf("_") + 1;
  return sPath.substring(0, iSuffixChar) + sNewChar + sPath.substring(iSuffixChar + 1);
}

function noOfMovieDisplayModes() {
  // ie non-hidden modes
  let movie = AppState.movie;
  let i = 0;
  Object
    .keys(movie.displayModes)
    .forEach(key => {
      var r = movie.displayModes[key].rect;
      if ((key != "hidden") && movie.displayModes[key].enabled) {
        i++;
      }
    });
  return i;
}

function computeCropRectForConvert(movie) {
  var rect = {
    x: 1,
    y: 1,
    width: 0,
    height: 0
  }; // This will be the cropped rect
  Object
    .keys(movie.displayModes)
    .forEach(key => {
      var r = movie.displayModes[key].rect;
      if ((key != "hidden") && movie.displayModes[key].enabled) {
        // horizontal adjust
        if (r.x < rect.x) {
          rect.x = r.x;
        }
        var rRight = r.x + r.width;
        var rectRight = rect.x + rect.width;
        if (rRight > rectRight) {
          rect.width += rRight - rectRight;
        }

        // vertical adjust
        if (r.y < rect.y) {
          rect.y = r.y;
        }
        var rTop = r.y + r.height;
        var rectTop = rect.y + rect.height;
        if (rTop > rectTop) {
          rect.height += rTop - rectTop;
        }
        // defensive!
        if (rect.x < 0.0) {
          rect.x = 0.0;
        }
        if (rect.y < 0.0) {
          rect.y = 0.0;
        }
        if (rect.width > 1.0) {
          rect.width = 1.0;
        }
        if (rect.height > 1.0) {
          rect.height = 1.0;
        }
      }

    });
  //return {x:0.0, y:0.0, width:1.0, height:1.0}; // for now!
  return rect;
}

function uploadCroppedRectVersionOfMediasetEditablesToMainJson(path, callback) {
  // Uploads a cloned copy of main.json - app (ie disk) main.json is unaffected.
  var mediasetEditablesClone = Utils.copyObj(AppState.mediasetEditables);
  for (var i = 0; i < mediasetEditablesClone.accompaniments.length; i++) {
    let croppedRect = computeCropRectForConvert(AppState.mediasetEditables.accompaniments[i].movie);
    var path = "records/mediaset/" + AppState.mediasetEditables.folderName + "/main.json";

    //var savedDisplayModeRect = {};
    var movie = mediasetEditablesClone.accompaniments[i].movie; // this local movie should override global movie
    // Modify display mode rects appropriately
    Object
      .keys(movie.displayModes)
      .forEach(key => {
        if ((key != "hidden") && movie.displayModes[key].enabled) {
          var r = movie.displayModes[key].rect;
          //savedDisplayModeRect[key] = copyObj(r);
          r.x -= croppedRect.x;
          //r.width = r.width * (1.0 - croppedRect.x);
          r.width = r.width / croppedRect.width;
          r.y -= croppedRect.y;
          //r.height = r.height * (1.0 - croppedRect.y);
          r.height = r.height / croppedRect.height;
        }
      });
    // var sMainJson = JSON.stringify(mediasetEditables); Restore rects to same as
    // app.
    /*Object.keys(movie.displayModes).forEach(key => {
            if ( (key != "hidden") && movie.displayModes[key].enabled) {
                var r = movie.displayModes[key].rect;
                r = copyObj(savedDisplayModeRect[key]); // copyObj nec?
            }
        });
        */
  }
  var sMainJson = JSON.stringify(mediasetEditablesClone);
  return GCS.insertStringObject(path, sMainJson, callback);
}

