export function mixWavFiles(sModeParam, callback) { //inputFiles, filter, outputFile)
  // Mixes files to mixed.wav using ffmpeg
  let data = {}
  data.inputFileNames = [];
  let sFilter = ' -filter_complex "';
  // eg : -filter_complex
  // "[0]volume=1.25[s];[1]volume=.1[t];[s][t]amix=inputs=2:duration=first"
  let sFilterSuffix = "";
  let iFile = 0;
  let fred = AudioTracks_.audioTrackArray;
  for (var i in AudioTracks_.audioTrackArray) {
    let audioTrack = AudioTracks_.audioTrackArray[i];
    if (audioTrack != null) {
      let fileId = audioTrack.fileId;

      let yMix;
      switch (sModeParam) {
        case "publish": // always exclude player track
          yMix = fileId == AudioTracks_.PLAYER_TRACK_ARRAY_IND
            ? false
            : audioTrack.yMix; // ie exclude player track
          break;
        case "preview": // include player track whether enabled or not
          yMix = fileId == AudioTracks_.PLAYER_TRACK_ARRAY_IND
            ? true
            : audioTrack.yMix;
          break;
        default: // follow yMix track val
          yMix = audioTrack.yMix;
          break;
      }
      /*      let yMix = fileId == AudioTracks_.PLAYER_TRACK_ARRAY_IND
        ? sModeParam == "preview"
        : audioTrack.yMix; // don't normally mix player track*/
      if (yMix) {
        //sFiles = sFiles + "-i " + destSubpath + sFiles.name + " "
        data.inputFileNames[iFile] = AudioTrack_.computeWavFileName(fileId);
        sFilter = sFilter + "[" + iFile + "]volume=" + audioTrack.volume + "[t" + iFile + "];"
        sFilterSuffix = sFilterSuffix + "[t" + iFile + "]";
        iFile = iFile + 1;
      }
    }
  }
  data.sParams = sFilter + sFilterSuffix + 'amix=inputs=' + iFile + ':duration=first" '; // duration=first REDUNDANT!? REMOVE
  data.outputFileName = "mixed.wav";
  AppComms.sendRequestToApp("execFfmpegCommand", data, function (jsonFromApp) {
    if (!Server.handleError(jsonFromApp, callback)) {
      callback(jsonFromApp);
    }
  });
}

export function makeMenuInfoAudioClip(outputFileName, callback) {
  // ffmpeg -y -i ./mixed.wav -ss 3.0 -t 5.0 -filter:a "afade=in:st=4:d=2,
  // afade=out:st=7:d=2"  clipfadeinout.mp3 We specify outputFileName so can
  // produce preview.wav for creatoreditor which is (currently) easier to play by
  // app sound codethan mp3 file. When publishing we set outputFileName param to
  // null to produce the mp3 file.
  let data = {};
  data.inputFileNames = ["mixed.wav"];
  data.sParams = '-b:a ' + sAudioBitRate + ' -ss ' + AppState.accompaniment.previewSecondsIn + ' -t ' + previewClipSeconds + ' -filter:a "afade=in:st=' + AppState.accompaniment.previewSecondsIn + ':d=' + fadeSecs + ', afade=out:st=' + (AppState.accompaniment.previewSecondsIn + previewClipSeconds - fadeSecs) + ':d=' + fadeSecs + '"';
  // let clippedFileName = replaceSuffixChar(AppState.accompanimentMp4FileName,
  // "C");
  data.outputFileName = outputFileName || AppState
    .accompanimentMp4FileName
    .substring(0, AppState.accompanimentMp4FileName.lastIndexOf("_")) + ".mp3";
  pushUpload(data.outputFileName, "audio/mpeg");
  AppComms.sendRequestToApp("execFfmpegCommand", data, function (jsonFromApp) {
    if (!Server.handleError(jsonFromApp)) {}
    callback(jsonFromApp);
  });
}
