export function mixAndReplaceVideoAudioForPublishedFilesIfNec(callback) {
  if (AudioTracks_.iComputeNonPlayerAudioTracksToMix() > 0) {
    mixWavFiles("publish", function (jsonFromApp) {
      if (!Server.handleError(jsonFromApp, callback)) {
        replaceVideoAudio("P", function (jsonFromApp) {
          if (!Server.handleError(jsonFromApp, callback)) {
            mixWavFiles("preview", function (jsonFromApp) {
              if (!Server.handleError(jsonFromApp, callback)) {
                makeMenuInfoAudioClip(null, function () {
                  callback(jsonFromApp);
                });
              }
            });
          }
        });
      }
    });
  } else { //iTracksToMix < 1!
    Server.errorCallback(callback, "Must mix at least one track - eg movie track!");
  }
}

export function replaceVideoAudio(sNewSuffixChar, callback) {
  // ffmpeg -y -i ./another_tune_in_b_flat_accomp_L.mp4 -i ./mixed.wav -c:v copy
  // -map 0:v:0 -map 1:a:0 new.mp4
  let data = {}
  data.inputFileNames = [];
  let s = AppState.accompanimentMp4Path;
  // let videoFileName =
  // AppState.accompanimentMp4FileNamePublishing.replaceSuffixChar(AppState.accomp
  // a nimentMp4F i leName, sNewSuffixChar)
  data.inputFileNames[0] = AppState.accompanimentMp4FileName; // eg swap _E for _L
  data.inputFileNames[1] = "mixed.wav"; // typically just created with mixWavFiles
  data.sParams = " -c:v copy -map 0:v:0 -map 1:a:0 ";
  data.outputFileName = replaceSuffixChar(AppState.accompanimentMp4FileName, "P");

  AppComms.sendRequestToApp("execFfmpegCommand", data, function (jsonFromApp) {
    if (!Server.handleError(jsonFromApp)) {
      callback(jsonFromApp);
    }
  });
}

