function uploadFiles(uploadArray, callback) {
  // Note that we upload modified main.json (eg with cropped rects) at very end
  // using insertString! Because existence of main.json is elsewhere used as sign
  // all uploaded ok.
  let uploadInd = 0;
  var fileNameLabel = document.getElementById("modalPopupLabel"); //MovieFilePickerfileNameLabel");
  var progressElt = document.getElementById("modalPopupProgress");

  function abortUploads() {
    alert("abortUploads");
    Popup.popupProgressMessage();
    //callback() //???
  }

  function getFileFromAppAndUploadToCloud() {
    var uploadFile = uploadArray[uploadInd];
    var fileName = uploadFile.fileName;
    //var path = jsonFromApp.data.mediasetFolderPath + "/" + fileName;
    let original = selectedRowInfo.original;
    var sublocationFilePath = AppState.sublocationFolderPath + fileName;

    progressElt.value = 0;
    Popup.popupMessageNew();
    Popup.popupProgressMessage("Uploading file: " + fileName + " to cloud.");
    AppComms.sendRequestToApp("getDiskFileContentsAs64", {
      sublocationFilePath: sublocationFilePath
    }, function (jsonFromApp) {
      if (!Server.handleError(jsonFromApp, abortUploads)) {
        var len = jsonFromApp.fileContentsBase64.length;
        if (jsonFromApp.fileContentsBase64.length == jsonFromApp.len) {
          GCS
            .insertObjectFromGui(fileName, uploadFile.mimeType, fileNameLabel, jsonFromApp.fileContentsBase64, "mediaset", AppState.mediasetEditables.folderName, progressElt, function (dataFromUpload) {
              if (!Server.handleError(dataFromUpload, abortUploads)) {
                uploadInd++;
                if (uploadInd < uploadArray.length) {
                  getFileFromAppAndUploadToCloud();
                } else { // Files successfully converted and uploaded
                  callback();
                  //Popup.popupProgressMessage();
                }
              }
            });
        } else {
          abortUploads();
        }
      }
    });
  }
  getFileFromAppAndUploadToCloud();
}

