// ========================= My stuff ==================
let cssStyle;

export function copyObj(obj) {
  return JSON.parse(JSON.stringify(obj));
}

export function getVisibilityObject(y) {
  return y == true
    ? {
      display: 'inline'
    }
    : {
      display: 'none'
    };
}

export function clearDropdownOptions(dropdownId) {
  var select = document.getElementById(dropdownId);
  var length = select.options.length;
  for (let i = 0; i < length; i++) {
    select.options[0] = null;
  }
}

export function setDropdownOptionByValue(dropdownId, value) {
  var select = document.getElementById(dropdownId);
  for (var i = 0; i < select.options.length; i++) {
    if (select.options[i].value === value) {
      select.options[i].selected = true;
    }
  }
}

export function setVisible(elt, yVal) {
  /*var elt = $(eltId);
    if (yVal) {
        elt.show();
    } else {
        elt.hide();
    }*/
  if (typeof elt === 'string' || elt instanceof String) {
    elt = document.getElementById(elt);
  }
  if (yVal) {
    elt.style = "display:inline;";
  } else {
    if (elt == null) {
      var z = 0;
    }
    elt.style = "display:none;";
  }
}

export function isVisible(elt) {
  //if (elt.style != null) {
  if (typeof(elt.style) != 'undefined' && elt.style != null) {
    return elt.style.display != "none";
  } else {
    return false; //?
  }
}

export function twoDecimalPlaces(f) {
  return Math.round(f * 100) / 100
}

export function getRequestParam(name) {
  // if(name=(new
  // RegExp('[?&]'+encodeURIComponent(name)+'=([^&]*)')).exec(location.search))
  if (name = (new RegExp('[?&]' + encodeURIComponent(name) + '=([^&]*)')).exec(window.location.search)) {
    return decodeURIComponent(name[1]);
  }
}

var BASE64_MARKER = ';base64,';

export function convertDataURIToBinary(dataURI) {
  var base64Index = dataURI.indexOf(BASE64_MARKER) + BASE64_MARKER.length;
  var base64 = dataURI.substring(base64Index);
  return convertBase64ToUint8Array(base64);
}

export function convertBase64ToUint8Array(base64) {
  let rawLength = 0;
  let raw;
  if (base64 != null) {
    raw = window.atob(base64);
    rawLength = raw.length;
  }
  var array = new Uint8Array(new ArrayBuffer(rawLength));

  for (var i = 0; i < rawLength; i++) {
    array[i] = raw.charCodeAt(i);
  }
  return array;
}

export function convertBase64ToInt8Array(base64) {
  let rawLength = 0;
  let raw;
  if (base64 != null) {
    raw = window.atob(base64);
    rawLength = raw.length;
  }
  var array = new Int8Array(new ArrayBuffer(rawLength));

  for (var i = 0; i < rawLength; i++) {
    array[i] = raw.charCodeAt(i);
  }
  return array;
}

export function isChrome() {
  var isChromium = window.chrome,
    winNav = window.navigator,
    vendorName = winNav.vendor,
    isOpera = winNav
      .userAgent
      .indexOf("OPR") > -1,
    isIEedge = winNav
      .userAgent
      .indexOf("Edge") > -1,
    isIOSChrome = winNav
      .userAgent
      .match("CriOS");

  if (isIOSChrome) {
    return true;
  } else if (isChromium !== null && typeof isChromium !== "undefined" && vendorName === "Google Inc." && isOpera === false && isIEedge === false) {
    return true;
  } else {
    return false;
  }
}

export function splitToPathAndFile(wholePath) {
  let afterLastSlashInd = wholePath.lastIndexOf("/") + 1;
  // let s = "Path: " + wholePath.substring(0, afterLastSlashInd) + "\nFile: " +
  // wholePath.substring(afterLastSlashInd)
  let s = wholePath.substring(0, afterLastSlashInd) + "<br>" + wholePath.substring(afterLastSlashInd)
  return s;
}

