export function removeExtAndDot(fileName) {
  let iDot = fileName.lastIndexOf(".");
  return fileName.substring(0, iDot);
}

export function removeExtAndDotAndSuffix(sPath) {
  let iSuffixChar = sPath.lastIndexOf("_") + 1;
  return sPath.substring(0, iSuffixChar - 1);;
}

export function getCssPxInt(sVarName) {
  if (cssStyle == null) {
    cssStyle = getComputedStyle(document.body);
  }
  var i = parseInt(cssStyle.getPropertyValue('--' + sVarName)); // parseInt seems to handle this fine despite the px suffix! */
  if (i) {
    return i;
  } else {
    alert("invalid css var name!");
    return 0; //??
  }
}

// ============================ Cookie functions - see
// https://www.w3schools.com/js/js_cookies.asp //============================

export function setCookie(cname, cvalue, exdays) {
  var d = new Date();
  d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
  var expires = "expires=" + d.toUTCString();
  document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
}

export function getCookie(cname) {
  var name = cname + "=";
  var decodedCookie = decodeURIComponent(document.cookie);
  var ca = decodedCookie.split(';');
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) == 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}

export function checkCookie() {
  var user = getCookie("username");
  if (user != "") {
    alert("Welcome again " + user);
  } else {
    user = prompt("Please enter your name:", "");
    if (user != "" && user != null) {
      setCookie("username", user, 30);
    }
  }
}
// ======================================== End of Cookie functions
// ========================================


export function parseJson(s) {
  //var json;
  try {
    return JSON.parse(s);
  } catch (e) {
    return null;//{error: "json parse error: " + s};
  }
}



