export function uploadPickedMidiFileToApp(event, recType, folderName) {
  var fileData = event.target.files[0];

  function moveUploadeFileToFolder() {
    var data = {};
    var fileName = event.target.files[0].name
    data.origFileName = fileName;

    function abort(resp) {
      alert(resp.error);
      Popup.popupMessageNew();
    }
    AppComms
      .sendRequestToApp("moveUploadedFile", data, function (resp) {
        if (!Server.handleError(resp)) {
          mediasetEditables.notesFileName = fileName;
          //alert("sendMediasetEditablesToNotesGameAppAndRelaunch no longer exists!");
          sendMediasetEditablesToNotesGameApp(null, function (resp) { // Saves to main.json
            if (!Server.handleError(resp)) {
              AppComms
                .sendRequestToApp("relaunch", null, function (resp) {
                  if (!Server.handleError(resp, abort)) {
                    getMediasetEditables(function (resp) {
                      if (!Server.handleError(resp, abort)) {
                        handleMediasetEditablesFromApp(resp);
                        Popup.popupMessageNew();
                      }
                    });
                  }
                });
            }
          });
        }
        //else {
        //  alert()
        //}
      });
  }
  Popup.popupMessageNew("Saving " + fileData.name + " for app");
  AppComms.uploadFileToApp(event, recType, folderName, moveUploadeFileToFolder);
}

function deducePlatform() {
  $platform = Utils.getRequestParam("platform");
  if ($platform == null) {
    // Simple device detection from browser
    var isiOS = navigator
      .userAgent
      .toLowerCase()
      .match('ipad') || navigator
      .userAgent
      .toLowerCase()
      .match('iphone') || navigator
      .userAgent
      .toLowerCase()
      .match('ipod');
    var isAndroid = navigator
      .userAgent
      .toLowerCase()
      .match('android');
    var isWindows = navigator
      .userAgent
      .toLowerCase()
      .match('windows');
    if (isiOS) {
      $platform = 'ios';
    } else if (isAndroid) {
      $platform = 'android';
    } else if (isWindows) {
      $platform = 'windows';
    }
  }
}

function deduceThisVersion() {
  var path = window
    .location
    .pathname
    .substr(1);
  var secondSlashInd = path.indexOf("/")
  return secondSlashInd >= 0
    ? path.substr(0, secondSlashInd)
    : "dev";
}

function deduceWebview() {
  var $webview = Utils.getRequestParam("webview");
  if ($webview == null) {
    $webview = "false";
  }
}

