  let iLastSlash = accompanimentMp4Path.lastIndexOf("/");
  accompanimentMp4FileName = accompanimentMp4Path.substring(iLastSlash + 1);
  accompanimentMp4FileBaseName = Utils.removeExtAndDotAndSuffix(accompanimentMp4FileName);
  menuInfoSnapshotFileName = accompanimentMp4FileBaseName + ".jpg";

  
