export function sendMediasetEditablesToNotesGameApp(sModeParam, callback) {
  // Will cause mediaset to be updated and main.json to be updated on disk and
  // dislpay to be updated (redrawn, relaunched) Note that (currently) will cause
  // code in NotesGame to be executed (while waiting for rightFirstNote) - so
  // mediaset is available etc var yGetEditables = yGetEditablesParam || false;
  var sMode = sModeParam || "update"
  // AppComms.setAppResponseCallbackFunction(callback); var yRelaunch = false; var
  // yRedraw = false;
  var json = {};
  var data = {};
  mediasetEditables.sCommand = null;
  switch (sMode) {
    case "getOnly":
    data = {};
      json.sNotesGameCommand = "getMediasetEditables";
      break;
    case "saveMovieSnapshot":
      //mediasetEditables = {}; //!!
      json.sNotesGameCommand = "saveMovieSnapshot";
      data.secondsIn = 0; // for now
      break;
    case "update":
      json.sNotesGameCommand = "updateMediaset";
      data.mediasetEditables = mediasetEditables;
      data.yRedraw = false; //yAppRedrawOnNextSend;
      break;
    case "updateAndRedraw":
      json.sNotesGameCommand = "updateMediaset";
      data.mediasetEditables = mediasetEditables;
      data.yRedraw = true;
      break;
    case "moveAppMovieToMovieSecondsInAndGetSnapshot":
      json.sNotesGameCommand = "moveAppMovieToMovieSecondsInAndGetSnapshot";
      data.movieSecondsIn = Movie_.movieSecondsIn;
      break;
    default:
      alert("Illegal sMode!");
  }
  json.command = "execInNotesGame";
  json.data = data;
//  json.mediasetEditables = mediasetEditables;
  // json.emptyResponse = yEmptyResponse || false; // ie don't send
  // "execInNotesGame" response - send "response" instead. json.data = {};//??
  // json.accompInd = accompInd + 1; // plus one = translate to lua
  // json.sDisplayMode = sDisplayMode;  json.deviceSizePx = deviceSizePx
  AppComms.setJsonMessageForApp(json, callback);
  //yAppRedrawOnNextSend = yRedraw; // redundant?
  
}

function validateInRange(str, min, max) {
  // https://stackoverflow.com/questions/12063070/javascript-validate-numeric-only
  // -and-within-range
  var n = parseFloat(str);
  return (!isNaN(n) && n >= min && n <= max);
}

function uploadCroppedRectVersionOfMediasetEditablesToMainJson(path, croppedRect, nextMethod) {
  var mediasetEditablesClone = Utils.copyObj(mediasetEditables);
  for (var i = 0; i < mediasetEditablesClone.accompaniments.length; i++) {
    //var savedDisplayModeRect = {};
    var movie = mediasetEditablesClone.accompaniments[i].movie; // this local movie should override global movie
    // Modify display mode rects appropriately
    Object
      .keys(movie.displayModes)
      .forEach(key => {
        if ((key != "hidden") && movie.displayModes[key].enabled) {
          var r = movie.displayModes[key].rect;
          //savedDisplayModeRect[key] = copyObj(r);
          r.x -= croppedRect.x;
          //r.width = r.width * (1.0 - croppedRect.x);
          r.width = r.width / croppedRect.width;
          r.y -= croppedRect.y;
          //r.height = r.height * (1.0 - croppedRect.y);
          r.height = r.height / croppedRect.height;
        }
      });
    // var sMainJson = JSON.stringify(mediasetEditables); Restore rects to same as
    // app.
    /*Object.keys(movie.displayModes).forEach(key => {
            if ( (key != "hidden") && movie.displayModes[key].enabled) {
                var r = movie.displayModes[key].rect;
                r = copyObj(savedDisplayModeRect[key]); // copyObj nec?
            }
        });
        */
  }
  var sMainJson = JSON.stringify(mediasetEditablesClone);
  return GCS.insertStringObject(path, sMainJson, nextMethod);
}

