function computeAudioEltId(thisz) {
  let s = "recordAudio" + thisz.props.track.fileId;
  return s;
}

function computeWavFilePath(fileId) {
  return AppState.accompanimentMp4Path + ("_R" + fileId.toString() + ".wav");
}

export function computeWavFileName(fileId) {
  return AppState.accompanimentMp4FileName + ("_R" + fileId.toString() + ".wav");
}
