function getOrCreateMediaElt(thisz, src) {
  // saved by (! last comment) -
  // https://stackoverflow.com/questions/43942544/control-audio-tag-via-react-stat
  // e return thisz._audio[thisz.props.track.fileId];
  let i = thisz.props.track.fileId;
  let elt = AudioTracks_.audioTrackArray[i].audioObject; //audioArray[i];
  if (elt == null) {
    elt = new Audio("data:audio/wav;base64," + src);
    AudioTracks_.audioTrackArray[i].audioObject = elt; //audioArray[i] = elt;
  }
  return elt;
}

