  componentDidMount() {
    playIconId = document.getElementById("playIconId");
    recordIconId = document.getElementById("recordIconId");
    recordIconId = document.getElementById("stopIconId");
    let fileId = this.props.track.fileId;
    let waveFilePath = computeWavFilePath(fileId);
    yPlaying = false;
    yRecording = false;
    let thisz = this;

    if ((fileId > 0) && !Boolean(this.props.track.audioObject) && AudioTracks_.existingWavFiles[fileId]) {
      // Avoid reloading (and movie audio is part of mp4) and only load if exists in
      // existingWavFiles
      AppComms
        .sendRequestToApp("mediaFileExists", ({
          fileName: computeWavFileName(this.props.track.fileId)
        }), function (jsonFromApp) {
          if (!Server.handleError(jsonFromApp)) {
            if (jsonFromApp.data.yFileExists) {}
            let audioTrack = AudioTracks_.audioTrackArray[fileId]
            audioTrack.audioObject = true;
            if (audioTrack.fileUnexpectedlyExists) {
              // Should not happen but incorporate it to allow delete/retain
              AudioTracks_.saveAudioTracksToApp();
              audioTrack.fileUnexpectedlyExists = false;
            }
            thisz.forceUpdate();
          } else {
            alert("Failed to find audio - please record again!")
          }
        });
    }
  }

  onDeleteTrackButtonHandler(e) {
    // hideAll(); MediasetPreviewz.videoPreviewClip.pause(); // In case playing
    // preview NOTE!! Should pause any/all plays here!
    let thisz = this;
    function remove() {
      AudioTracks_.nullOrRemoveNewMovieTrack(thisz.props.track.fileId);
      AudioTracks_.saveAudioTracksToApp();
      thisz
        .props
        .forceAudioTracksComponentUpdate();
    }

    // Delete any corresponding file
    let wavFileName = computeWavFileName(this.props.track.fileId);
    if (this.props.track.audioObject && wavFileName != null) {
      AppComms
        .sendDeleteSubBucketFileCommandToApp("created", AppState.mediasetEditables.folderName, wavFileName, "mediaset", function (jsonFromApp) {
          if (!Server.handleError(jsonFromApp)) {
            remove();
          }
        });
    } else {
      remove();
    }
  }
