  handlePlayOrStopIconPressNewer(e) {
    if (!yPlaying) { // ie Stop iconpressed
      yPlaying = true;
      let data = {};
      data.fileName = computeWavFileName(this.props.track.fileId);
      data.firstNoteSecondsIn = AppState.accompaniment.audio.firstNoteSecondsIn;
      data.volume = this.props.track.volume;
      AppComms.sendRequestToApp("playWavFile", data, function (jsonFromApp) {
        if (!Server.handleError(jsonFromApp)) {
          let fred = 0;
        }
      });
    } else {
      yPlaying = false;
      AppComms.sendRequestToApp("stopWavFile", null, function (jsonFromApp) {
        if (!Server.handleError(jsonFromApp)) {
          let fred = 0;
        }
      });
    }
    this.forceUpdate();
  }

  handlePlayOrStopIconPress(e) {
    let thiz = this;
    function finishedPlay() {
      //yPlaying = false;
      thiz.handlePlayOrStopIconPress();
      Popup.popupMessageNew();
    }
    AppComms
      .sendRequestToApp("stopWavFile", null, function (jsonFromApp) {
        if (!Server.handleError(jsonFromApp)) {
          if (!yPlaying) { // ie Stop iconpressed
            yPlaying = true;
            //AudioTracks_.trackPlaying
            let fileName = computeWavFileName(thiz.props.track.fileId)
            Popup.popupMessageNew("Playing file: " + Utils.splitToPathAndFile(computeWavFilePath(thiz.props.track.fileId)), "Stop", finishedPlay);
            AppComms.sendRequestToApp("stopWavFile", null, function (jsonFromApp) {
              if (!Server.handleError(jsonFromApp)) {
                let data = {};
                data.fileName = fileName;
                data.firstNoteSecondsIn = AppState.accompaniment.audio.firstNoteSecondsIn;
                data.volume = thiz.props.track.volume;
                AppComms.sendRequestToApp("playWavFile", data, function (jsonFromApp) {
                  if (!Server.handleError(jsonFromApp)) {
                    playFinishedTimeoutId = setTimeout(finishedPlay, (AppState.movieSecondsDuration - AppState.accompaniment.audio.firstNoteSecondsIn) * 1000);
                  }
                });
              }
            });
          } else {
            if (playFinishedTimeoutId) {
              clearTimeout(playFinishedTimeoutId);
            }
            yPlaying = false;
          }
          thiz.forceUpdate();
        }
      });
  }

