  handleRecordIconPress(e) {
    if (this.props.track.fileId > 0) { // ie not movie track (defensive)
      // let sExt = ("_R" + this.props.track.fileId.toString() + ".wav"); let thisz =
      // this; recordTrack(sSuffix, audioElt, function () { thisz.forceUpdate(); });
      let thisz = this;
      function cancelRecord() {
        AppComms.setAwaitingCommandResponse(false); // ie stop expecting response from setRecordTrack to allow new command
        AppComms.sendRequestToApp("setPreventPlay", ({"preventPlay": true}), function (jsonFromApp) {
          if (!Server.handleError(jsonFromApp)) {
            Popup.popupMessageNew();
          }
        });
      }
      Popup.popupMessageNew("Place earphones and record track.", "Cancel", cancelRecord); // we need  cancel on this popup
      //waveFilePath = computeWavFilePath(); //AppState.accompanimentMp4Path + sExt;
      let waveFilePath = computeWavFilePath(this.props.track.fileId);
      AppComms.sendRequestToApp("setRecordTrack", ({waveFilePath: waveFilePath}), function (jsonFromApp) {
        Popup.popupMessageNew();
        if (!Server.handleError(jsonFromApp)) {
          let yFirstRecord = !thisz.props.track.audioObject;
          AudioTracks_.audioTrackArray[thisz.props.track.fileId].audioObject = true; // recorded!
          AudioTracks_.audioTrackArray[thisz.props.track.fileId].yMix = true;
          if (yFirstRecord) {
            AudioTracks_.saveAudioTracksToApp();
          }
          thisz.props.track.audioObject = true;
          thisz
            .props
            .forceAudioTracksComponentUpdate(); //so lists existingWavFiles to include new file! RWe could set single elt true here instead - but this is simple and robust.
        }
      });
    }
  }

