  computeSpeakerClassName(e) {
    let className;
    let padding = "";
    if (this.props.track.audioObject) {
      if (this.props.track.yMix) {
        className = "fa fa-volume-up fa-lg speaker-on";
      } else {
        className = "fa fa-volume-off fa-lg speaker-off";
        padding = '&nbsp;&nbsp'; // compensate for missing sound lines
      }
    } else {
      className = "fa fa-volume-up fa-lg speaker-hidden";
    }
    return className; //React.createElement('i', 'className=' + className, "");//>{padding}</i>');
  }

  computeIfSpeakerPadding(e) {
    let yPadding = false;
    if (this.props.track.audioObject) {
      if (!this.props.track.yMix) {
        yPadding = true; //'&amp;nbsp;';//nbsp;\&nbsp';
      }
    }
    return yPadding;
  }

  computePlayIconClassName(e) {
    !yPlaying && !yRecording && this.props.track.audioObject
    if (!this.props.track.audioObject) {
      return "recordButton-off";
    } else if (yPlaying) {
      return "speaker-off";
    } else {
      return "speaker-on";
    }
  }
  //        <audio id={computeAudioEltId(this) }/>
