/**
     * Google Cloud Storage API request to retrieve the list of buckets in
     * your Google Cloud Storage project.
     */
function listBuckets() {
  var request = window
    .gapi
    .client
    .storage
    .buckets
    .list({'project': PROJECT});
  executeRequest(request, 'listBuckets');
}

