export function deleteRecordFolderFiles(recType, recordFolderName, nextMethod) {
  // If folder contains "main.json" we will delete that first! (because existence
  // of main.json is used as check for valid (set of) mediaset (files))
  var folderName = 'records/' + recType + '/' + recordFolderName + '/';
  var request = window
    .gapi
    .client
    .storage
    .objects
    .list({'bucket': AppState.bucketName, 'prefix': folderName});

  function abort(resp) {
    // Note that this may be called eg after main.json successfulyy deleted - will
    // currently cause alert and harmless invitation to try again.
    if (nextMethod) {
      nextMethod(resp);
    }
  }

  request
    .execute(function (resp) {
      if (!Server.handleError(resp, abort)) {
        // jQuery.ajaxSetup({async:false}); Following will only delete one file and if
        // no files to delete will not execute nextMethod!!???? How the hell to delete
        // indefinite no of files asynchronously and at end call nextMethod?
        let iDeleted = 0;
        let iFiles = resp.items != null
          ? resp.items.length
          : 0;
        let mainJsonInd = -1;
        if (iFiles > 0) {
          for (var i in resp.items) {
            if (resp.items[i].name.endsWith("main.json")) {
              mainJsonInd = i;
              break;
            }
          }
          if (mainJsonInd >= 0) { // delete main.json and only then delete rest of items
            var request = window
              .gapi
              .client
              .storage
              .objects
              .delete({'bucket': AppState.bucketName, 'object': resp.items[mainJsonInd].name});
            request.execute(function (resp) {
              if (!Server.handleError(resp, abort)) {
                if (iFiles > 1) {
                  iDeleted = 1;
                  loopDelete();
                } else { // single file was main.json and has been deleted!
                  updateCloudLatestEditTime(function (resp) {
                    if (!Server.handleError(resp, abort)) {
                      nextMethod(resp);
                    }
                  });
                }
              }
            });
          } else {
            loopDelete();
          }
          function loopDelete() {
            for (var i in resp.items) { // ie for each (latest) definition of record type
              if (i != mainJsonInd) { // avoid trying to delete already deleted main.json
                var item = resp.items[i];
                var request = window
                  .gapi
                  .client
                  .storage
                  .objects
                  .delete({'bucket': AppState.bucketName, 'object': item.name});
                request.execute(function (resp) {
                  if (!Server.handleError(resp, abort)) {
                    if (++iDeleted == iFiles) {
                      updateCloudLatestEditTime(function (resp) {
                        if (!Server.handleError(resp, abort)) {
                          nextMethod(resp);
                        }
                      });
                    }
                  }
                });
              }
            }
          } // end of loopDelete
        } else {
          nextMethod(resp); // what is resp value?? Clearer if null?
        }
      }
    });
}

