export function insertStringObject(objectName, sText, nextMethodParam) {
  // returns true on success else false updates latestEditTime.json see line 69 of
  // https://code.google.com/p/google-api-javascript-client/source/browse/GettingS
  // tarted.wiki?repo=wiki&r=7a5c2bf011f427aa77482be709e22f4bef787f17 see -
  // https://cloud.google.com/storage/docs/json_api/v1/how-tos/upload this is json
  // api (?) - see -
  // https://cloud.google.com/storage/docs/json_api/v1/objects/insert
  Auth
    .authorize(function (data) {
      if (!Server.handleError(data)) {
        var request = window
          .gapi
          .client
          .request({
            'path': '/upload/storage/' + API_VERSION + '/b/' + AppState.bucketName + '/o', // like in the multipart request!
            'params': {
              'name': objectName
            },
            'method': 'POST',
            'body': sText
          });
        var nextMethod = nextMethodParam || function dummyMethod() {};
        var data = {};
        function handleGcsError(resp, nextMethod, sMsgPrefixParam) {
          if (resp.error) {
            sMsgPrefixParam = sMsgPrefixParam || "Failed to insert string into cloud - "
            data.error = sMsgPrefixParam + resp.error.message;
            alert(data.error); // for now
            nextMethod(data);
            return true;
          } else {
            return false;
          }
        }
        request
          .execute(function (resp) {
            if (!handleGcsError(resp, nextMethod)) {
              if (sText != "latestEditTime.json") {
                var time = Date.now();
                var t = {};
                t.localTime = time;
                var json = JSON.stringify(t, null, null);
                // insertStringObject('latestEditTime.json', json); // replaced by following!
                // ==============================  update last update time!
                // ==================================
                var request = window
                  .gapi
                  .client
                  .request({
                    'path': '/upload/storage/' + API_VERSION + '/b/' + AppState.bucketName + '/o', // like in the multipart request!
                    'params': {
                      'name': 'latestEditTime.json'
                    },
                    'method': 'POST',
                    'body': json
                  });
                request.execute(function (resp) {
                  if (!handleGcsError(resp, nextMethod, "Failed to update last edit time - ")) {
                    data.resp = resp;
                    nextMethod(data);
                  }
                });
              }
            }
          });
      }
    });
}

