        var contentType = fileData.type || 'application/octet-stream';
        var metadata = {
          'name': sGcsUploadFolder + fileData.name,
          'mimeType': contentType
        };

        var base64Data = btoa(reader.result);
        var multipartRequestBody = delimiter + 'Content-Type: application/json\r\n\r\n' + JSON.stringify(metadata) + delimiter + 'Content-Type: ' + contentType + '\r\nContent-Transfer-Encoding: base64\r\n' +
        //'Cache-Control: private, max-age=0, no-transform\r\n' +
        '\r\n' + base64Data + close_delim;

        var oReq = new XMLHttpRequest();
