/**
     * Authorize Google Cloud Storage API.
     */
function checkAuthNB(nextMethod) {
  window
    .gapi
    .auth
    .authorize({
      client_id: Auth.clientId, scope: scopes, immediate: false // ie Non-Blocking
    }, nextMethod);
}

function checkAuth(nextMethod) {
  // Could easily make this non blocking!?? Note that we do initial authorise and
  // call handleAuthorise result which displays button if nec
  function callNextMethodIfExists(authResult) {
    if (nextMethod) {
      nextMethod(authResult); // pass on authResult for now because initGapi wants it
    }
  }
  function getTokenAndDoNextMethod(authResult) {
    if (authResult && !authResult.error) {
      G_authResult = authResult;
      callNextMethodIfExists(authResult);
    } else {
      alert("Auth failed!!"); // or present button?? or what?
      Auth.authorizeButton.style.visibility = '';
      Auth.authorizeButton.onclick = checkAuth(nextMethod); //??

    }
  }
  var timeNowMs = new Date().getTime();
  if ((timeNowMs - lastAuthTimeMs) > checkAuthTimeoutMs) {
    lastAuthTimeMs = timeNowMs;
    window
      .gapi
      .auth
      .authorize({
        client_id: Auth.clientId, scope: scopes, immediate: false // was true // ?????
      }, getTokenAndDoNextMethod);
  } else {
    callNextMethodIfExists();
  }
}

function createRecord(recType, jsonTable, recFolder) {
  if (records[recType] == null) {
    records[recType] = []; //{records : [], H_iJsonInd : 0, H_iMaxCloudFolderInt : 0};
    records[recType].H_iJsonInd = 0;
    records[recType].H_iMaxCloudFolderInt = 0;
    records[recType].H_folderNames = [];
  }
  var record = records[recType]
  jsonTable.H_folderName = recFolder;
  record.push(jsonTable);
  // record.H_folderNames.push({folderName:recFolder}); Update
  // H_iMaxCloudFolderInt for record type for getting unique name for potential
  // new folder under record type (eg under mediaset) (this why my record names
  // end up as numbers)
  if (recFolder >= 0 || recFolder < 0) { // ie if numeric
    var iCloudFolderInt = Math.floor(recFolder);
    if (iCloudFolderInt > record.H_iMaxCloudFolderInt) {
      record.H_iMaxCloudFolderInt = iCloudFolderInt;
    }
  }
  return record;
}

