/*
    DESCRIPTION
        We place video with two canvases above it (dislpay: absolute) one for selection mask and the other for the cropping center.
        We make the canvases slightly larger then the video to give click space when selection hole is at edge of video.
        To place them so that they surround video evenly we need to offset them by frame width times scale.
        Note that actual canvas width (ie on lower level than my movieCanvasWidth/Height logic) currently simply needs to be big
        enough to cover biggest video. Maybe we should change this. It does allow more dragging/clicking space in south and east dirs...

    SCALE
        We descale mouse input over canvas in updateMousePos()  (also generates mouseDx/Dy)
        We address the canvas space using video coords. (ie we don't need to scale them)

    MIRROR
        Note that when we mirror the movie horizontally we must not change the mediasetEditables rect or cropping center only flipX flag.
        Therefore we have several flipX checks to accomplish flipped display without saving inverted rect or croppingCenter to app.

    TODO
        Stop top jumping to cursor when selected?

    IMPROVEMENTS
        initialisation of vars in handleMediasetEditables should include flipX, croppingCenter etc - tidy
        handlePossibleSelectionChange should not need redraw for full screen mode
            would also need code in lua?




*/
