function prepareCanvas(canvas, context) {
  // movieCanvasWidth and sCanvasFrameOffsetStyle must have been set!
  resetCanvasMatrix(context);
  clearCanvas(context); //clear old!
  canvas.width = canvasWidth;
  canvas.height = canvasHeight;
  canvas.style = sCanvasFrameOffsetStyle; // It may have changed due to html element expansion above movie grid.
}

function setupCanvasesAndMasks() {
  // var heightAvailable = mainTableWidth + (gridColumns - 1) * gridGap - 2 *
  // moviePadding; Movie.fZoom =
  // widthAvailable/movieWidth;//movie.textureSize.width;//.8 ;//* .97;//.97;//-
  // ie who knows what happens if doesn't fit in table! var actualMovieHeight =
  // movieDiv.clientHeight - 2 * movieFrameThicknessPx - 20; // what about outer
  // margin? var distortion = var fZoomY = 1.0;//actualMovieHeight/movieHeight;
  var martha = Movie.movieDiv;
  var marthaz = Movie.roundedCornerCanvasClearance;
  canvasWidth = Movie.movieDiv.clientWidth - Movie.roundedCornerCanvasClearance * 2;
  // var gridGap =
  // parseInt(getComputedStyle(Movie.movieDiv).getPropertyValue("grid-gap")); //
  // Fudge! Currently using duplicated grid-gap in .mycell because the one in
  // .twelveColGrid did not work!? var canvasWidthz = AppState.mainTableWidth +
  // (AppState.gridColumns - 1) * 10  - Movie.roundedCornerCanvasClearance * 2;
  // canvasWidth is equal to canvasWidth here!

  canvasHeight = Movie.movieDiv.clientHeight - Movie.roundedCornerCanvasClearance * 2;
  // Change canvas size to fit Movie.movieDiv -
  // https://stackoverflow.com/questions/331052/how-to-resize-html-canvas-element
  var fred = Movie.accompVideo.offsetTop;
  var accompVideo = Movie.accompVideo;
  sCanvasFrameOffsetStyle = "left: " + (Movie.accompVideo.offsetLeft - Movie.canvasMovieOverlap) + "px; top: " + (Movie.accompVideo.offsetTop - Movie.canvasMovieOverlap) + "px;";

  // ================= Set up masksCanvas ================

  prepareCanvas(masksCanvas, masksCanvasCtx);

  if (AppState.accompaniment != null) { // Set up hole vars from mediaset displaymode rect
    var accomp = AppState.accompaniment;
    //var movieSize = AppState.movieTextureSize;//accomp.movie.textureSize;
    var movieRect = Utils.copyObj(accomp.movie.displayModes[AppState.sDisplayMode].rect);
    movieRect.y = 1.0 - (movieRect.y + movieRect.height); // App and canvas have opposite y-axis signs
    if (AppState.flipX) {
      movieRect.x = 1.0 - movieRect.x - movieRect.width;
    }
    var framePx = Movie.canvasMovieOverlap * 2;
    holeLeft = Movie.canvasMovieOverlap + movieRect.x * (canvasWidth - framePx);
    holeRight = Movie.canvasMovieOverlap + (movieRect.x + movieRect.width) * (canvasWidth - framePx);
    var fred = Movie.canvasMovieOverlap;
    holeTop = Movie.canvasMovieOverlap + movieRect.y * (canvasHeight - framePx);
    holeBottom = Movie.canvasMovieOverlap + (movieRect.y + movieRect.height) * (canvasHeight - framePx);

    minHoleWidth = (canvasWidth - framePx) / 10;
    minHoleHeight = (canvasHeight - framePx) / 10;
  } else {}

  drawSelectionMask();

  // ================= Set up topCanvas for cropping center (full screen only) and
  prepareCanvas(topCanvas, topCanvasCtx);

  topCanvas.style = sCanvasFrameOffsetStyle;
  if (AppState.yFullScreenMode) {
    // =========== Draggable cropping center ===========
    topCanvas.hidden = false;
    drawCroppingCenter(); //
  } else {
    topCanvas.hidden = false;
  }
  //if (!yMouseHandlersAdded) {  addMouseHandlers(); }
}

function drawCroppingCenter() {
  var cx = holeLeft + (holeRight - holeLeft) * croppingCenter.x;
  var cy = holeTop + (holeBottom - holeTop) * (1.0 - croppingCenter.y);
  drawCrosshairs(cx, cy);
}

function addMouseHandlers() {
  topCanvas
    .addEventListener('mousedown', function (e) {
      updateCursor(); // before setting yMouseDown
      yMouseDown = true;
      downPos = updateMousePos(masksCanvas, e);
      console.log(" " + downPos.x + " " + downPos.y);
      if (crossBarsContains(downPos.x, downPos.y)) {
        // anchor of rect is top left corner
        croppingCenterMouseDown = true;
        drawCrosshairs(downPos.x, downPos.y);
      }
    });
  topCanvas.addEventListener('mousemove', function (e) {
    // I have integrated cursor type changeing in with selection moves etc!?
    var pos = updateMousePos(masksCanvas, e);
    var x = pos.x;
    var y = pos.y;
    var dx = 0;
    var dy = 0;
    var left,
      right,
      top,
      bottom; // we avoid possible arithmetic plus followed by minus problems.
    left = holeLeft;
    right = holeRight;
    top = holeTop;
    bottom = holeBottom;

    if (!yMouseDown) {
      updateCursor();
    }
    // Compute new hole vars
    if (yMouseDown && !croppingCenterMouseDown) {
      var yDrag = false;
      switch (mouseDownArea) {
        case "N":
          top += mouseDy;
          break;
        case "NE":
          top += mouseDy;
          right += mouseDx;
          break;
        case "E":
          right += mouseDx;
          break;
        case "SE":
          bottom += mouseDy;
          right += mouseDx;
          break;
        case "S":
          bottom += mouseDy;
          break;
        case "SW":
          bottom += mouseDy;
          left += mouseDx;
          break;
        case "W":
          left += mouseDx;
          break;
        case "NW":
          top += mouseDy;
          left += mouseDx;
          break;
        case "H": // ie the selection "Hole" - the whole hole moves :)
          right += mouseDx;
          left += mouseDx;
          top += mouseDy;
          bottom += mouseDy;
          yDrag = true;
          break;
        case "X": // ie outside canvas area
          break;
        default:
          alert("oops");
          //break;

      }
      // ============ Validate selection "hole"vars =============
      if (yDrag) { // prevent from going over edge of video
        if ((left < Movie.canvasMovieOverlap) || (right > canvasWidth - Movie.canvasMovieOverlap) || (right - left < minHoleWidth)) {
          // Revert
          right = holeRight;
          left = holeLeft;
        }
        if ((top < Movie.canvasMovieOverlap) || (bottom > canvasHeight - Movie.canvasMovieOverlap) || (bottom - top < minHoleHeight)) {
          // var off = Movie.accompVideo..offsetTop - (canvas.offsetTop);// +
          // movieFrameThicknessPx); if ( (top < off1 || (bottom > movieCanvasHeight -
          // movieFrameThicknessPx)  || (bottom - top < minHoleHeight) ) { Revert
          bottom = holeBottom;
          top = holeTop;
        }
      } else { // go right to edge of video if mouse has passed it.
        if (left < Movie.canvasMovieOverlap) {
          left = Movie.canvasMovieOverlap;
        }
        if (right > canvasWidth - Movie.canvasMovieOverlap) {
          right = canvasWidth - Movie.canvasMovieOverlap;
        }
        if (right - left < minHoleWidth) {
          // Revert
          right = holeRight;
          left = holeLeft;
        }
        if (top < Movie.canvasMovieOverlap) {
          top = Movie.canvasMovieOverlap;
        }
        if (bottom > canvasHeight - Movie.canvasMovieOverlap) {
          bottom = canvasHeight - Movie.canvasMovieOverlap;
        }
        if ((right - left < minHoleWidth) || (bottom - top < minHoleHeight)) {
          // Revert
          top = holeTop;
          bottom = holeBottom;
        }
      }
      holeLeft = left;
      holeRight = right;
      holeTop = top;
      holeBottom = bottom;
      drawSelectionMask()

      if (AppState.yFullScreenMode) { // update cropping center position
        var cx = holeLeft + (holeRight - holeLeft) * croppingCenter.x;
        var cy = holeTop + (holeBottom - holeTop) * (1.0 - croppingCenter.y);
        //dragRect.drawAt(cx, cy);
        drawCrosshairs(cx, cy);
      }
    }
    if (croppingCenterMouseDown) {
      var dragX = x,
        dragY = y;
      // Keep it in the selection hole.
      if (x < holeLeft) {
        dragX = holeLeft
      };
      if (x > holeRight) {
        dragX = holeRight
      };
      if (y < holeTop) {
        dragY = holeTop
      };
      if (y > holeBottom) {
        dragY = holeBottom
      };
      drawCrosshairs(dragX, dragY);
      // Deduce new cropping center from position in hole.
      croppingCenter.x = (dragX - holeLeft) / (holeRight - holeLeft);
      croppingCenter.y = 1.0 - (dragY - holeTop) / (holeBottom - holeTop);
    }

  });
  topCanvas.addEventListener('mouseup', function (e) {
    yMouseDown = false;
    croppingCenterMouseDown = false;
    topCanvas.style.cursor = "default"; //nec?
    handlePossibleSelectionChange();
  });
  //yMouseHandlersAdded = true;
}

function updateCursor() {
  var x = curPos.x;
  var y = curPos.y;
  var c = "default"
  var clickArea = "X";
  //console.log("x = " + x + " y = " + y);
  if ((x >= 0) && (y >= 0)) {
    if (x <= holeLeft) {
      if (y <= holeTop) {
        c = "nw-resize";
        clickArea = "NW";
      } else if (y <= holeBottom) {
        c = "ew-resize";
        clickArea = "W";
      } else if (y <= canvasHeight) {
        c = "sw-resize";
        clickArea = "SW";
      }
    } else if (x <= holeRight) {
      if (y <= holeTop) {
        c = "n-resize";
        clickArea = "N";
      } else if (y <= holeBottom) {
        c = "move"; // selection space
        clickArea = "H";
      } else if (y <= canvasHeight) {
        c = "s-resize";
        clickArea = "S";
      }
    } else if (x <= canvasWidth) {
      if (y <= holeTop) {
        c = "ne-resize";
        clickArea = "NE";
      } else if (y <= holeBottom) {
        c = "ew-resize";
        clickArea = "E";
      } else if (y <= canvasHeight) {
        c = "se-resize";
        clickArea = "SE";
      }
    }
  }

  if (!yMouseDown) { // ie stop updating it after yMouseDown set.
    mouseDownArea = clickArea;
    topCanvas.style.cursor = c; // move to mosemov?
  }

}

function clearSelectionMask() {
  masksCanvasCtx.clearRect(0, 0, canvasWidth, canvasHeight);
}

function clearCanvas(canvasContext) {
  canvasContext.clearRect(0, 0, canvasWidth, canvasHeight);
}

function drawSelectionMask() {
  // We draw a polgon with a hole! -
  // https://stackoverflow.com/questions/13618844/polygon-with-a-hole-in-the-middl
  // e -with-html5s-canvas masksCtx.restore();
  clearSelectionMask(); //masksCtx.clearRect(0, 0, 2000, 1000);//movieCanvasWidth, movieCanvasHeight);
  masksCanvasCtx.globalAlpha = maskAlpha;
  var w = canvasWidth; //movieWidth;
  var h = canvasHeight; //movieHeight;
  //   masksCtx.fill();
  masksCanvasCtx.beginPath();
  //outer shape, clockwise
  masksCanvasCtx.moveTo(0, 0);
  masksCanvasCtx.lineTo(w, 0);
  masksCanvasCtx.lineTo(w, h);
  masksCanvasCtx.lineTo(0, h);
  masksCanvasCtx.closePath();
  //inner shape (hole), counter-clockwise
  masksCanvasCtx.moveTo(holeLeft, holeTop);
  masksCanvasCtx.lineTo(holeLeft, holeBottom);
  masksCanvasCtx.lineTo(holeRight, holeBottom);
  masksCanvasCtx.lineTo(holeRight, holeTop);
  masksCanvasCtx.closePath();
  //fill
  masksCanvasCtx.fillStyle = mainMaskColor;
  masksCanvasCtx.fill();

  drawDeviceCroppingMasksIfNec();

  masksCanvasCtx.globalAlpha = 1.0;
}

function drawDeviceCroppingMasksIfNec() {
  if (AppState.yFullScreenMode) {
    var holeWidth = holeRight - holeLeft;
    var holeHeight = holeBottom - holeTop; // - 5 is correct (for horizontal crop) - Why on earth!??
    masksCanvasCtx.clearRect(holeLeft, holeTop, holeWidth, holeHeight);
    masksCanvasCtx.fillStyle = deviceCroppingMaskColor;
    masksCanvasCtx.globalAlpha = maskAlpha;
    var holeAspectRatio = holeWidth / holeHeight;
    var fCrop;
    // Our basic equation: AppState.deviceAspectRatio * (1 - fCrop) =
    // holeAspectRatio - Solve for fCrop: fCrop = 1.0 -
    // holeAspectRatio/AppState.deviceAspectRatio
    if (holeAspectRatio > AppState.deviceAspectRatio) { // horizontal crop
      var holeLeftToUse,
        holeRightToUse,
        cropLeftPixels,
        cropRightPixels;
      fCrop = 1.0 - AppState.deviceAspectRatio / holeAspectRatio;
      var cropPixels = holeWidth * fCrop; // * croppingCenter.x;
      if (AppState.flipX) {
        holeLeftToUse = holeRight;
        holeRightToUse = holeLeft;
        cropLeftPixels = -cropPixels * (1.0 - croppingCenter.x);
        cropRightPixels = cropPixels * croppingCenter.x;
      } else {
        holeLeftToUse = holeLeft;
        holeRightToUse = holeRight;
        cropLeftPixels = cropPixels * croppingCenter.x;
        cropRightPixels = -cropPixels * (1.0 - croppingCenter.x);
      }
      // masksCtx.fillRect(holeLeftToUse + 1, holeTop + 1, cropLeftPixels, holeHeight
      // - 2); masksCtx.fillRect(holeRightToUse - 1, holeTop + 1, cropRightPixels,
      // holeHeight - 2);
      masksCanvasCtx.fillRect(holeLeftToUse, holeTop, cropLeftPixels, holeHeight);
      masksCanvasCtx.fillRect(holeRightToUse, holeTop, cropRightPixels, holeHeight);
    } else { // vertical crop
      fCrop = 1.0 - holeAspectRatio / AppState.deviceAspectRatio;
      var cropPixels = holeHeight * fCrop; // * croppingCenter.x;
      // masksCtx.fillRect(holeLeft + 1, holeTop + 1, holeWidth - 2, cropPixels * (1.0
      // - croppingCenter.y)); masksCtx.fillRect(holeLeft + 1, holeBottom - 1,
      // holeWidth - 2, -cropPixels * croppingCenter.y);
      masksCanvasCtx.fillRect(holeLeft, holeTop, holeWidth, cropPixels * (1.0 - croppingCenter.y));
      masksCanvasCtx.fillRect(holeLeft, holeBottom, holeWidth, -cropPixels * croppingCenter.y);
    }
    masksCanvasCtx.globalAlpha = 1.0;
  }
}
