function updateMousePosOld(canvas, evt) {
  // Returns descaled position over canvas and computes mouseDx/Dy
  var rect = canvas.getBoundingClientRect();
  var p = {
    x: (evt.clientX - rect.left) / Movie.fZoom, //* scaleX,   // scale mouse coordinates after they have
    y: (evt.clientY - rect.top) / Movie.fZoom //* scaleY     // been adjusted to be relative to element
  }
  mouseDx = p.x - curPos.x;
  mouseDy = p.y - curPos.y;
  curPos.x = p.x;
  curPos.y = p.y;
  return curPos;
}

function updateMousePos(canvas, evt) {
  // Returns descaled position over canvas and computes mouseDx/Dy sets curPos
  // relative to movie (do we want position in movie?)
  var rect = canvas.getBoundingClientRect();
  var p = {
    x: (evt.clientX - rect.left), //* scaleX,   // scale mouse coordinates after they have
    y: (evt.clientY - rect.top) //* scaleY     // been adjusted to be relative to element
  }
  mouseDx = p.x - curPos.x;
  mouseDy = p.y - curPos.y;
  curPos.x = p.x;
  curPos.y = p.y;
  return curPos;
}

var topX = 0,
  topY = 0,
  prevCrosshairsX,
  preCrosbarsY;

function drawCrosshairs(x, y) {
  topX = x; //200;//canvas.width / 2;
  topY = y; //200;//canvas.height / 2;

  if (prevCrosshairsX != null) {
    topCanvasCtx.clearRect(prevCrosshairsX - topRadius - 1, prevCrosshairsY - topRadius - 1, topRadius * 2 + 2, topRadius * 2 + 2);
  }
  // Draw circle
  topCanvasCtx.beginPath();
  topCanvasCtx.arc(topX, topY, topRadius, 0, 2 * Math.PI, false);
  topCanvasCtx.closePath();
  topCanvasCtx.strokeStyle = '#00ff00'; //'#228B22';//#44ff44';//'#000000';//'#0000ff';
  topCanvasCtx.stroke();

  // Draw crosshairs
  topCanvasCtx.beginPath();
  // topCtx.fillStyle = 'red'; topCtx.fill();
  topCanvasCtx.lineWidth = 1;
  // topCtx.closePath();
  topCanvasCtx.moveTo(topX - topRadius, topY);
  topCanvasCtx.lineTo(topX + topRadius, topY);
  topCanvasCtx.moveTo(topX, topY - topRadius);
  topCanvasCtx.lineTo(topX, topY + topRadius);
  topCanvasCtx.closePath();
  topCanvasCtx.strokeStyle = '#00ff00'; //'#ffffff';//'#00ff00';
  topCanvasCtx.stroke();

  prevCrosshairsX = x;
  prevCrosshairsY = y;

  drawDeviceCroppingMasksIfNec();

}

function crossBarsContains(x, y) {
  var dx = topX - x;
  var dy = topY - y;
  return dx * dx + dy * dy < topRadius * topRadius;
}

var prevHoleLeft,
  prevHoleRight,
  prevHoleTop,
  prevHoleBottom;
function handlePossibleSelectionChange(d) {
  if ((holeLeft != prevHoleLeft) || (holeRight != prevHoleRight) || (holeTop != prevHoleTop) || (holeBottom != prevHoleBottom) || (croppingCenter.x != prevCroppingCenter.x) || (croppingCenter.y != prevCroppingCenter.y)) {
    // send new info to app Extract selected rectangle data. Note that hole coords
    // are non-scaled coords over whole canvas var rectWidth = (holeRight -
    // holeLeft)/movieWidth;
    var movieCanvasWidth = canvasWidth - Movie.canvasMovieOverlap * 2;
    var movieCanvasHeight = canvasHeight - Movie.canvasMovieOverlap * 2;
    var rectWidth = (holeRight - holeLeft) / movieCanvasWidth;
    //var rectHeight = (holeBottom - holeTop)/movieHeight;
    var rectHeight = (holeBottom - holeTop) / movieCanvasHeight;
    //var rectX = (holeLeft - movieFrameThicknessPx)/movieWidth;
    var rectX = (holeLeft - Movie.canvasMovieOverlap) / movieCanvasWidth;
    //var flipX = displayMode.flipX;
    if (AppState.flipX) {
      rectX = 1.0 - rectX - rectWidth;
    }
    var rectY = (holeTop - Movie.canvasMovieOverlap) / movieCanvasHeight;

    AppState.displayMode.rect = {
      x: rectX,
      y: 1.0 - rectHeight - rectY,
      width: rectWidth,
      height: rectHeight
    };

    prevHoleLeft = holeLeft;
    prevHoleRight = holeRight;
    prevHoleTop = holeTop;
    prevHoleBottom = holeBottom;
    if (AppState.yFullScreenMode) {
      prevCroppingCenter.x = croppingCenter.x;
      prevCroppingCenter.y = croppingCenter.y;
      // AppState.displayMode.rect = {x : rectX, y : 1.0 - rectHeight - rectY, width :
      // rectWidth, height : rectHeight};

      AppState.displayMode.croppingCenter = Utils.copyObj(croppingCenter); //{x:croppingCenter.x, y:1.0 - croppingCenter.y} ;
      if (AppState.flipX) {
        AppState.displayMode.croppingCenter.x = 1.0 - croppingCenter.x;
      }
    }
    //yAppRedrawOnNextSend = true;
    AppState.sendMediasetEditablesToNotesGameAppAndRedraw();
  }
}



