/*
DESCRIPTION
	Comms link with app which contains http server.
	Raw sockets are not generally available on all browsers yet ie we can not send messages to browser but only in response to http message sent from browser.
	So we implement a running loop where browser is polling server for commands.

	Note  (the intention is) that all commands are sent from browser to app.
  They are sent as json with a command field and various other data fields.
  Commands are executed one by one. Commands are now queued - ie thay can now be called before the previous command has responded.
  Because the previous command needs to be 'finished' before next can be started, all commands to app must now be responded to.
  In a response, if the command field contains "response" then the browser calls appResponseCallbackFunction if it was set when the message was sent to app.

	Another special case is that of downloading a (picked) file to app.
  App<->Browser FILE TRANSFER
    We have achieved transfer in both directions of pretty large files (39Mb) at high speed which means we can plausibly dispense with running file picker from app and do
    it within the browser instead - ie we transfer the picked file contents from browser to the app for ffmpeg conversion etc.
    Could maybe halve the PC memory use on browser->app if we used post to return file using file handle (as per msdn example code).
    Necessarily simultaneous file-transfer memory requirement currently:
      app->browser
        string in app
        string in browser
        (maybe string in app is/can be freed here? Not - because garbage collection can not be forced in javascript - but likely if we null then will be collected if need the memory for decode!)
        decoded string
      browser->app
        string from file
        encoded string
        free string from file here
        encoded string in browser

	The other extreme could be multipart using POST in either direction (what was the 2048 buffer in example? Seems to be reading 'entity' onece request has arrived.)
		Or alternatively my own multipart logic (quite simple with such reliable connection etc) - this could be a future upgrade.
	For now (ie unless/until we get memory limitation problems):
		Use GET for everything, including app->browser file transfer, and POST just for browser->app file transfer.
		Send file by POST which then just saves file and then in callback a get command to copy and rename the file as desired. YES - OK!

	If browser starts up first:
		It polls the app and times out if no answer (with popup message)
	If app starts first:
    It operates as for player until it receives command from browser (will need to clear popup).


WINDOW MANIPULATION (ie under win32)
  NOTE: We can easily deploy a react project build at a subdir of website (is this start_url in manifest.json?)
  	When we click menu in app then
		if menu browser window set then
      switch windows to browser window (if known)
    else
      launch menu (eg at menu/index.html) with domain dependent on ctrl key
      if ctrl pressed
        creator/index.html
      else
        menu/index.html
    end
    Note
    Also include menu.html and creator.html in website root which just redirect to /menu/index.html and creator/index.html
    If menu project is located above creator project with includes to share files then which version of creator should files be included from?
    Also include user info when starting menu from app - for now?
      No point since next we need to:
        Replace server redirection system with gapi client










*/
