export function initComms(browserWindowTypeParam, yDefaultMediaEditModeParam, callback) {
  //alert("initComms");
  browserWindowType = browserWindowTypeParam;
  browserWindowId = new Date().getTime();

  yDefaultMediaEditMode = yDefaultMediaEditModeParam;

  xhttp = new XMLHttpRequest();
  xhttp.onreadystatechange = onreadystatechange;
  xhttp.onerror = onResponseError;
  xhttp.onabort = function (evt) {
    var z = 0;
  }
  //xhttp.onload = function (evt) { 	var z = 0; }
  xhttp.onprogress = function (evt) {
    console.log("progress");
  }
  /* Following actually prevented upload from working!!?
    xhttp.upload.onprogress = function(evt) {
      console.log("upload progress");
    }
    */

  url = "http://localhost:" + appServerPort + "/server/?";
  yAwaitingResponse = false;
  yAwaitingCommandResponse = false;
  sendToApp(); // Start the comms loop!
  let sInstigator = Utils.getRequestParam("instigator");
  // if (sInstigator != "vscode") { // We want to be pretty sure that browser
  // window is in foreground at this point! (so app can deduce it's window handle)

  sendRequestToApp("getAppInfo", null, function (resp) {
    if (!Server.handleError(resp)) {
      //Server.version = resp.data.serverVersion;
      if (AppState.sMenuSource == "created" && (resp.data.creatorBucket.length == 0 || resp.data.creatorEmail.length == 0) ) {
        alert("Missing bucketName or userEmail!!!!!!")
        //here we should auth (or rather re-sign in) and try again!?
      }
      Server.setBaseUrl(resp.data.serverBaseUrl); //"http://" + resp.data.serverVersion + ".mos-cloud.appspot.com/";
      Website.setBaseUrl(resp.data.websiteBaseUrl); //"http://www.mindofsound.com/" + resp.data.websiteVersion + "/";
      AppState.setUserEmail(resp.data.creatorEmail);
      AppState.setBucketName(resp.data.creatorBucket);
      if (!Website.baseUrl.startsWith(window.location.origin)) {
        window.location = Website.baseUrl;
      } else { //continue!
        if (true) { //sInstigator == "app") {
          getThisBrowserWindowHandleAndArrange(null, callback);
        } else { // not app
          //"getVersions"
          callback();
        }
      }
    }
  });
}

function getThisBrowserWindowHandleAndArrange(pxWidthParam, callback) {
  let pxWidth = pxWidthParam || (AppState.editorWindowWidth + 30); // Why need + 30!!????? AFter this we get windows.innerwidth=864 and outerWidth=880 (= 850 + 30)
  let data = {
    "editorWindowWidth": pxWidth
  }
  sendRequestToApp("getThisBrowserWindowHandleAndArrange", data, function (jsonFromApp) {
    if (!Server.handleError(jsonFromApp)) {
      //alert("hWnd=" + jsonFromApp.data.browserWindowHandle);
      setWindowHandle(jsonFromApp.data.browserWindowHandle);
      callback()
    } else {
      alert("getThisBrowserWindowHandleAndArrange failed!"); // need handleabort function to handle error!?
    }
  });
}

