export function sendDeleteSubBucketFileCommandToApp(location, folderName, fileName, recordTypeParam, callback) {
  var recordType = recordTypeParam || "mediaset"
  var data = {};
  data.subBucketPath = "records/" + recordType + "/" + folderName + "/" + fileName;
  data.location = location || "downloaded";
  sendRequestToApp("deleteSubBucketFile", data, callback);
}

export function sendRequestToApp(sCommand, data, callback) {
  var json = {};
  json.command = sCommand;
  /*
if (sCommand == "uploadFileToApp") {
    ySendNextMessageAsPost = true; // json.command will be ignored
  } else {
    json.command = sCommand;
  }
  */
  json.data = data;
  setJsonMessageForApp(json, callback);
}

export function sendRequestToNotesGameApp(sNotesGameCommand, data, callback) {
  // ie this assumes that NotesGame is running and is waiting for right note!
  let json = {};
  json.command = "execInNotesGame";
  json.sNotesGameCommand = sNotesGameCommand;
  json.data = data;
  setJsonMessageForApp(json, callback);
}

