export function uploadFileToApp(evt, recType, folderName, callback) {
  /*We can simply pass the picked file to xhttp.send - see https://stackoverflow.com/questions/6211145/upload-file-with-ajax-xmlhttprequest
	  and then we don't have any significant memory use.
	  We upload to a standard folder (C\mindofsound\temp) as "uploadedFile" and then check success and if ok then copy it to required dest (if specified).
		We do it this way around (ie rather than preparing for upload first with file dest etc) because:
			Avoids possible half formed files all over the place!??
			Simpler to create json responses from lua!
			We can do all conversion in same temp folder - after which it is easy to clean up.
	*/
  var fileData = evt.target.files[0];
  ySendNextMessageAsPost = true; //This will cause bypass the normal command route and upload file to app in c++
  setJsonMessageForApp(fileData, function (jsonFromApp) {
    if (!Server.handleError(jsonFromApp)) {
      var data = {};
      data.fileSize = fileData.size
      sendRequestToApp("handleUploadedFile", data, function (jsonFromApp) {
        if (!Server.handleError(jsonFromApp)) {
          callback(evt, recType, folderName);
        }
      });
    }
  });
}

export function getMediaFileFromAppAsBase64(sublocationFilePath, elt, sType, callback) {
  sendRequestToApp("getDiskFileContentsAs64", {
    sublocationFilePath: sublocationFilePath
  }, function (jsonFromApp) {
    if (!Server.handleError(jsonFromApp)) {
      if (jsonFromApp.fileContentsBase64 == null) {
        alert("Failed to get media from this location - please try again!")
        if (callback != null) {
          callback();
        }
      } else {
        var len = jsonFromApp.fileContentsBase64.length;
        if (jsonFromApp.fileContentsBase64.length == jsonFromApp.len) {
          elt.onerror = function (evt) {
            alert("Failed to load media correctly!");
            if (callback != null) {
              callback("Failed to load media correctly!");
            }
          };
          let iExt = sublocationFilePath.lastIndexOf(".") + 1;
          let sExt = sublocationFilePath.substr(iExt);
          //accompVideo.src = "data:image/png;base64," + jsonFromApp.fileContentsBase64;
          elt.src = "data:" + sType + "/" + sExt + ";base64," + jsonFromApp.fileContentsBase64;
          //src="data:audio/ogg;base64
          callback();
        } else {
          alert("Warning! Failed to get media from app!");
        }
      }
    }
  });
}



