import React from 'react';
import ReactDOM from 'react-dom';

import * as AppState from './AppState';
import {Menu} from './components/Menu';
import * as Menu_ from './components/Menu';
import * as Server from './Server';
import * as AppComms from './AppComms';
import * as Creator from './Creator';
//import {selectedRowInfo} from './Menu';

function pubState(rowData) {
  //return Menu_.publishingStateDescriptions[rowData.publishingState];
  return rowData.producer;
}

