import React from 'react';

import './EditingGrid.css';

import * as AppState from '../AppState';
import Publishing from './Publishing';

class Music extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 'fred',
      title: 'Piecename'
    };

    this.onTitleChangeHandler = this
      .onTitleChangeHandler
      .bind(this);
    this.onComposerChangeHandler = this
      .onComposerChangeHandler
      .bind(this);
    this.onMidiInstrumentsChangeHandler = this
      .onMidiInstrumentsChangeHandler
      .bind(this);

    //this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    AppState.createAndInsertPickFileToHandleCombo("NotesFilePicker", ".mid", AppState.uploadPickedMidiFileToApp, "Change notes file"); //insertObjectFromGui);
    var NotesFilePickerfileNameLabel = document.getElementById("NotesFilePickerfileNameLabel");
    NotesFilePickerfileNameLabel.innerHTML = AppState.mediasetEditables.notesFileName;
    document.getElementById("quantizationDropdownId").value = AppState.mediasetEditables.quantization; // This also sets maskDropdown.selectedIndex correctly!
    document.getElementById("groupNotesByBeatCheckbox").value = AppState.mediasetEditables.groupNotesByBeat; // This also sets maskDropdown.selectedIndex correctly!
  }

  onTitleChangeHandler(event) {
    this.setState({title: event.target.value});
    AppState.mediasetEditables.title = event.target.value;
    AppState.sendMediasetEditablesToNotesGameApp();
  }

  onComposerChangeHandler(event) {
    this.setState({title: event.target.value});
    AppState.mediasetEditables.composer = event.target.value;
    AppState.sendMediasetEditablesToNotesGameApp();
  }

  onMidiInstrumentsChangeHandler(event) {
    this.setState({title: event.target.value});
    AppState.mediasetEditables.midiInstrument = event.target.value;
    AppState.sendMediasetEditablesToNotesGameApp();
  }

  onQuantizationDropdownChangeHandler(cb) {
    var val = cb.target.options[cb.target.selectedIndex].value;
    AppState.mediasetEditables.quantization = val
    //AppState.sendMediasetEditablesToNotesGameAppAndRedraw(); // could not get it to redraw iwth new quantization!? - so relaunch
    AppState.sendMediasetEditablesToNotesGameApp();
    AppState.relaunchApp();
  }

  onGroupNotesByBeatChangeHandler(e) {
    AppState.mediasetEditables.groupNotesByBeat = e.target.checked;
    AppState.sendMediasetEditablesToNotesGameAppAndRedraw();
  }

  /*handleSubmit(event) {
    alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
  }*/
  /*
  function onComposerChangeHandler(cb) {
    mediasetEditables.composer = cb.value;
    sendMediasetEditablesToNotesGameApp();
  }*/
  /*render() {
    return (
    <input type="text" value={this.state.value} onKeyUp={this.onTitleChangeHandler} />
    );
  }
}*/
  render() {
    return (
      <React.Fragment>
 
        <div className="splitGridRowLeft gridRowsHeading">
          
         <b>Music</b>
         </div>
         <div className="splitGridRowRight ">
         <i className="mediaEditorHeading floatRight">mindofsound media editor</i>
        </div>

        <div className="mycell splitGridRowLeft">Title&nbsp;&nbsp;
          <input
            type="text"
            name="Piece name"
            value={AppState.mediasetEditables.title}
            size="35"
            onChange={this.onTitleChangeHandler}/>
        </div>
        <div className="mycell splitGridRowRight">Composer&nbsp;&nbsp;
          <input
            type="text"
            name="Composer"
            value={AppState.mediasetEditables.composer}
            size="20"
            onChange={this.onComposerChangeHandler}/>
        </div>
        <div className="mycell splitGridRowLeft">
          <span id="NotesFilePicker">Notes file&nbsp;&nbsp;</span>
        </div>
        <div className="mycell splitGridRowRight">For midi instruments&nbsp;&nbsp;
          <input
            type="text"
            name="Midi Instruments"
            value="75"
            size="20"
            onChange={this.onMidiInstrumentsChangeHandler}/>
        </div>
        <div className="mycell splitGridRowLeft">
          <label>Quantization&nbsp;&nbsp;</label>
          <select
            name="quantizationDropdown"
            id="quantizationDropdownId"
            onChange={this.onQuantizationDropdownChangeHandler}>
            <option>Auto1</option>
            <option>1/32</option>
            <option>1/16</option>
            <option>1/8</option>
            <option>1/4</option>
          </select>

        </div>
        <div className="mycell splitGridRowRight">Group notes strictly by beat&nbsp;&nbsp;
          <input
                type="checkbox"
                id="groupNotesByBeatCheckbox"
                onChange={this.onGroupNotesByBeatChangeHandler}/>
        </div>


      </React.Fragment>
    );
  }
}

export default Music;



