import * as Utils from './Utils';
var modalPopup,
  modalPopupProgress,
  modalPopupLabel,
  closeModalPopupExitCross,
  modalPopupCommsLabel;
var yModalPopupLabelVisible,
  yCloseModalPopupExitCrossVisible,
  yModalPopupProgressVisible,
  yModalPopupCommsLabelVisible,
  yPopupCancelButtonVisible;
var popupMessageDiv,
  popupMessageLabel, modalPopupButton1, modalPopupButton2;

  
export function initModalPopup() {
  // Get the modal - https://www.w3schools.com/howto/howto_css_modals.asp
  modalPopup = document.getElementById('modalPopup');
  modalPopupLabel = document.getElementById('modalPopupLabel');
  modalPopupProgress = document.getElementById('modalPopupProgress');
  popupMessageDiv = document.getElementById('popupMessageDiv');
  popupMessageLabel = document.getElementById('popupMessageLabel');
  modalPopupButton1 = document.getElementById('modalPopupButton1');
  modalPopupButton2 = document.getElementById('modalPopupButton2');

  closeModalPopupExitCross = document.getElementById('closeModalPopupExitCross');
  modalPopupCommsLabel = document.getElementById('modalPopupCommsLabel');
  yModalPopupLabelVisible = false;
  yCloseModalPopupExitCrossVisible = false;
  yModalPopupProgressVisible = false;
  yModalPopupCommsLabelVisible = false;
  yPopupCancelButtonVisible = false;
  hidePopupElts();

  // Get the button that opens the modal var btn =
  // document.getElementById("modalPopupBtn"); Get the <span> element that closes
  // the modal
  var span = document.getElementsByClassName("close")[0];

  // When the user clicks on the button, open the modal btn.onclick = function() {
  // modalPopup.style.display = "block";    Utils.setVisible(modalPopup, true) } When
  // the user clicks on <span> (x), close the modal
  span.onclick = function () {
    //modalPopup.style.display = "none";
    Utils.setVisible(modalPopup, false)
  }

  // When the user clicks anywhere outside of the modal, close it
  /*window.onclick = function(event) {
        if (event.target == modal) {
            modalPopup.style.display = "none";
        }
    }*/
}

export function hidePopupElts() {
  Utils.setVisible(modalPopupLabel, false);
  Utils.setVisible(closeModalPopupExitCross, false);
  Utils.setVisible(modalPopupProgress, false);
  Utils.setVisible(modalPopupCommsLabel, false);
}

