export function popupMessageNewOld(msg, sButton, buttonHandler) {
  var yShow = (msg != null);
  popupMessageLabel.innerHTML = msg;
  //modalPopupLabel.innerHTML = msg;
  Utils.setVisible(popupMessageDiv, yShow);
  //Utils.setVisible(modalPopup, yShow);
  if (sButton) {
    //let buttonElt = document.getElementById("modalPopupButton1");
    Utils.setVisible(modalPopupButton1, yShow);
    modalPopupButton1.innerHTML = sButton;
    modalPopupButton1.onclick = buttonHandler;
  } else {
    Utils.setVisible(modalPopupButton1, false);
  }
}

export function popupMessageNew(msg, sButton1, button1Handler, sButton2, button2Handler) {
  var yShow = (msg != null);
  popupMessageLabel.innerHTML = msg;
  //modalPopupLabel.innerHTML = msg;
  Utils.setVisible(popupMessageDiv, yShow);
  //Utils.setVisible(modalPopup, yShow);
  if (sButton1) {
    //let buttonElt = document.getElementById("popupCancelButton");
    Utils.setVisible(modalPopupButton1, true);
    modalPopupButton1.innerHTML = sButton1;
    modalPopupButton1.onclick = button1Handler;
  } else {
    Utils.setVisible(modalPopupButton1, false);
  }
  if (sButton2) {
    //let buttonElt = document.getElementById("popupCancelButton");
    Utils.setVisible(modalPopupButton2, true);
    modalPopupButton2.innerHTML = sButton2;
    modalPopupButton2.onclick = button2Handler;
  } else {
    Utils.setVisible(modalPopupButton2, false);
  }
}

export function popupProgressMessage(msg, yExitParam) {
  // Calling with no msg will hide the popup !
  var yExit = yExitParam || false;
  // closeModalPopupExitCross.style.display = yExit? "inline" : "none";
  var yShow = (msg != null);
  yModalPopupLabelVisible = yShow;
  yModalPopupProgressVisible = yShow;
  modalPopupLabel.innerHTML = msg;
  Utils.setVisible(modalPopupLabel, yShow);
  Utils.setVisible(modalPopupProgress, yShow);
  yCloseModalPopupExitCrossVisible = yExit && yShow; //?
  Utils.setVisible(closeModalPopupExitCross, yCloseModalPopupExitCrossVisible);
  Utils.setVisible(modalPopup, yModalPopupLabelVisible || yModalPopupProgressVisible);
}

export function popupCommsMessage(msg, yExitParam) {
  // Calling with no msg will hide the popup unless was previous popup which will
  // then be restored. This would surely be simpler if we just made another modal
  // on higher z-level for comms! (would not need yModalPopupLabelVisible etc)
  // Remember what modals were visible already!
  var yShow = (msg != null);
  //if (yModalPopupCommsLabelVisible) {
  if (!(yModalPopupLabelVisible || yModalPopupProgressVisible)) {
    yModalPopupLabelVisible = Utils.isVisible(modalPopupLabel);
    yCloseModalPopupExitCrossVisible = Utils.isVisible(closeModalPopupExitCross);
    yModalPopupProgressVisible = Utils.isVisible(modalPopupProgress);
  } else {
    var z = 0;
  }
  hidePopupElts();

  var yExit = yExitParam || false;
  //closeModalPopupExitCross.style.display = yExit? "inline" : "none";
  yModalPopupCommsLabelVisible = yShow;
  modalPopupCommsLabel.innerHTML = msg;
  Utils.setVisible(modalPopupCommsLabel, yShow);
  yCloseModalPopupExitCrossVisible = yExit && yShow; //?
  Utils.setVisible(closeModalPopupExitCross, yCloseModalPopupExitCrossVisible);
  Utils.setVisible(modalPopup, yShow || yModalPopupLabelVisible || yModalPopupProgressVisible);

  if (!yShow) { // Revert to before comms popup
    Utils.setVisible(modalPopupLabel, yModalPopupLabelVisible);
    Utils.setVisible(closeModalPopupExitCross, yCloseModalPopupExitCrossVisible);
    Utils.setVisible(modalPopupProgress, yModalPopupProgressVisible);
  }
  //}
}


