/*
    Popup login is only triggered on google server if call was instigated by button press!

    How did login work in old (eg gcs.js)?
        We were using old auth - ie:  gapi.auth.authorize({client_id....}, handleAuthResult) called from a button press which triggers google server login page (if not already signed in).
            See my sample testMyOldAuthLogin.html
        Note that were ALSO using oauth2 to eg: gapi.client.oauth2.userinfo.get to get id,email, name of signed in user.


    Should we be using Oauth2? YES!
        We were using it to get id, email, name - but not with same calls.
        Main sample uses gapi.auth2.getAuthInstance().signIn/signOut methods - see testAuthLogin.html
            It also handles a change in who is signed in!?

        So what is oauth2 equivalent of gapi.auth.authorize which we use to get
        DOCS: https://developers.google.com/identity/protocols/OAuth2
            see "Client-side (JavaScript) applications"
                Note that this does not mention refresh tokens but says should simply repeate original authorize request (for new token)
                    This is precisely what we ere doing with old auth.

        QUESTION: We were using access tokens for gapi.client uploading multipart - but surely not for storage api?
            eg createBucket - we authorize first and seems that storage api knows about this internally.

  ???? - https://stackoverflow.com/questions/20922163/google-javascript-api-how-to-detect-user-sign-in-status

    OVERVIEW
      We replace out devious html relocation through ms-cloud server to get hold of email and bucketName, with new system as follows:
        Call gapi.client.plus.people.get to get google user id and email
        Call mos-cloud with id to get bucket name.


*/

